import React, { useEffect, useState } from "react";
import { supabase } from "./utils/supabaseClient";
import { Routes, Route, Link, Navigate, useNavigate } from "react-router-dom";
import Loader from "./components/Loader";
import SearchBar from "./components/SearchBar";
import GameCard from "./components/GameCard";
import AllGamesPage from "./pages/AllGamesPage";
import AllCollectionGamesPage from "./pages/AllCollectionGamesPage";
import SubscriptionInfo from "./pages/SubscriptionInfoPage";
import GameDetails from "./pages/GameDetailsPage";
import AddonDetails from "./pages/AddonDetailsPage";
import SearchResults from "./pages/SearchResultsPage";
import AdminPage from "./pages/AdminPage";
import CartPage from "./pages/CartPage";

import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

function App() {
  const [subscriptions, setSubscriptions] = useState([]);
  const [collections, setCollections] = useState([]);
  const [allGames, setAllGames] = useState([]); // Состояние для всех игр
  const [isLoading, setIsLoading] = useState(true);
  const [isDataLoaded, setIsDataLoaded] = useState(false);
  const [showSearchResults, setShowSearchResults] = useState(false);
  const navigate = useNavigate();

  const sliderSettings = {
    dots: false,
    infinite: false,
    initialSlide: 0,
    speed: 500,
    slidesToShow: 2.4,
    slidesToScroll: 2,
    // adaptiveHeight: true,
    // waitForAnimate: false,
    responsive: [
      {
        breakpoint: 9000,
        settings: {
          slidesToShow: 5,
          slidesToScroll: 5,
        },
      },
      {
        breakpoint: 1140,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 4,
        },
      },
      {
        breakpoint: 960,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
        },
      },
      {
        breakpoint: 767,
        settings: {
          arrows: false,
        },
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 2.4,
          slidesToScroll: 2,
          arrows: false,
        },
      },
      {
        breakpoint: 420,
        settings: {
          slidesToShow: 1.6,
          slidesToScroll: 1,
          arrows: false,
        },
      },
    ],
  };

  const sliderSettingsSubs = {
    dots: false,
    infinite: false,
    arrows: false,
    initialSlide: 0,
    slidesToShow: 3,
    slidesToScroll: 0,
    speed: 500,
    responsive: [
      {
        breakpoint: 9000,
        settings: {
          slidesToShow: 5,
          slidesToScroll: 5,
        },
      },
      {
        breakpoint: 960,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
        },
      },
    ],
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        // Получаем подписки
        const { data: subscriptionsData, error: subscriptionsError } =
          await supabase.from("subscriptions").select("*").order("id");

        if (subscriptionsError) throw subscriptionsError;

        // Получаем коллекции с играми
        const { data: collectionsData, error: collectionsError } =
          await supabase
            .from("collections")
            .select("*")
            .order("order", { ascending: true });

        if (collectionsError) throw collectionsError;

        // Получаем ВСЕ игры (включая те, что без коллекций)
        const { data: allGamesData, error: allGamesError } = await supabase
          .from("games")
          .select("*")
          .order("created_at", { ascending: false });

        if (allGamesError) throw allGamesError;

        // Для каждой коллекции получаем игры
        const collectionsWithGames = await Promise.all(
          collectionsData.map(async (collection) => {
            const { data: games, error: gamesError } = await supabase
              .from("games")
              .select("*")
              .eq("collection_title", collection.title)
              .order("created_at", { ascending: false });

            if (gamesError) throw gamesError;

            return {
              ...collection,
              games: games || [],
            };
          })
        );

        setSubscriptions(subscriptionsData);
        setCollections(collectionsWithGames);
        setAllGames(allGamesData || []);
      } catch (error) {
        console.error("Error fetching data:", error);
      } finally {
        setIsLoading(false);
        setIsDataLoaded(true); // Добавьте эту строку
      }
    };

    fetchData();
  }, []);

  const handleGameClick = (gameUrl) => {
    navigate(`/game/${encodeURIComponent(gameUrl)}`);
  };

  return (
    <>
      {isLoading && <Loader />}
      <Routes>
        <Route
          path="/"
          element={
            <div className="app">
              <div className="app__header">
                <SearchBar setShowSearchResults={setShowSearchResults} />
                <Link
                  to="/cart"
                  className="button cart button--cart"
                  title="Перейти в корзину"
                >
                  <img src="icons/icon-bag.svg" alt="Корзина" width="25" />
                  <span className="cart__counter">
                    {JSON.parse(localStorage.getItem("cart"))?.length || 0}
                  </span>
                </Link>
              </div>
              {!showSearchResults && (
                <>
                  {collections
                    .filter(
                      (collection) =>
                        collection.games && collection.games.length > 0
                    )
                    .map((collection) => (
                      <section
                        key={collection.id}
                        className="games-list-section"
                      >
                        <div className="games-list-header">
                          <h2 className="games-list-title">
                            {collection.title}
                          </h2>
                          {/* {collection.games.length > 10 && ( */}
                          <Link
                            to={`/collection/${collection.title}`}
                            className="button button--view-all"
                          >
                            Посмотреть все
                          </Link>
                          {/* )} */}
                        </div>
                        <Slider {...sliderSettings}>
                          {collection.games.slice(0, 10).map((game) => (
                            <GameCard
                              key={game.idGame}
                              game={game}
                              onClick={() => handleGameClick(game.gameUrl)}
                            />
                          ))}
                        </Slider>
                      </section>
                    ))}

                  <section className="games-list-section">
                    <div className="games-list-header">
                      <h2 className="games-list-title">Все игры</h2>
                      <Link to="/all-games" className="button button--view-all">
                        Посмотреть все
                      </Link>
                    </div>
                    <Slider
                      {...sliderSettings}
                      key={`all-games-slider-${allGames.length}`} // Добавьте ключ
                    >
                      {allGames.slice(0, 10).map((game) => (
                        <GameCard
                          key={game.idGame}
                          game={game}
                          onClick={() => handleGameClick(game.gameUrl)}
                        />
                      ))}
                    </Slider>
                  </section>

                  <section className="games-list-section">
                    <div className="games-list-header">
                      <h2 className="games-list-title">Подписки</h2>
                    </div>
                    <Slider {...sliderSettingsSubs}>
                      {subscriptions.map((subscription, index) => (
                        <li key={index} className="game-card">
                          <Link
                            to={`/subscription/${subscription.title}`}
                            className="game-card-link"
                          >
                            <div className="game-cover-wrapper">
                              <img
                                src={subscription.cover}
                                alt={subscription.title}
                                className="game-cover"
                              />
                            </div>
                            <h3 className="game-title">{subscription.title}</h3>
                            <span>
                              от{" "}
                              {
                                subscription.durations.find(
                                  (duration) => duration.duration === "1 месяц"
                                ).price
                              }
                            </span>
                          </Link>
                        </li>
                      ))}
                    </Slider>
                  </section>
                </>
              )}
            </div>
          }
        />
        <Route path="/all-games" element={<AllGamesPage />} />
        <Route
          path="/collection/:collectionTitle"
          element={<AllCollectionGamesPage />}
        />
        <Route path="/game/:gameUrl" element={<GameDetails />} />
        <Route path="/addon/:addonUrl" element={<AddonDetails />} />
        <Route
          path="/subscription/:subscriptionTitle"
          element={<SubscriptionInfo />}
        />
        <Route path="/search/:query" element={<SearchResults />} />
        <Route path="/admin" element={<AdminPage />} />
        <Route path="/cart" element={<CartPage />} />
        {/* <Route path="*" element={<Navigate to="/" />} /> */}
      </Routes>
    </>
  );
}

export default App;
