import React, { useEffect } from "react";

const Dialog = ({ isOpen, onClose, onConfirm, title, message }) => {
  // Закрытие модального окна при нажатии на Esc
  useEffect(() => {
    const handleKeyDown = (event) => {
      if (event.key === "Escape") {
        onClose();
      }
    };

    if (isOpen) {
      window.addEventListener("keydown", handleKeyDown);
    }

    return () => {
      window.removeEventListener("keydown", handleKeyDown);
    };
  }, [isOpen, onClose]);

  // Закрытие модального окна при клике за его пределами
  const handleOverlayClick = (event) => {
    if (event.target === event.currentTarget) {
      onClose();
    }
  };

  if (!isOpen) return null;

  return (
    <div className="dialog" onClick={handleOverlayClick}>
      <div className="dialog__content">
        <h2 className="dialog__title">{title}</h2>
        <p className="dialog__message">{message}</p>
        <div className="dialog__actions">
          <button className="button button--delete" onClick={onConfirm}>
            Удалить
          </button>
          <button className="button button--cancel" onClick={onClose}>
            Отмена
          </button>
        </div>
      </div>
    </div>
  );
};

export default Dialog;
