import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { supabase } from "../utils/supabaseClient";
import Loader from "../components/Loader";
import BackButton from "../components/BackButton";
import GameCard from "../components/GameCard";

const SearchResults = () => {
  const { query } = useParams();
  const navigate = useNavigate();
  const [results, setResults] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [page, setPage] = useState(0);
  const [hasMore, setHasMore] = useState(true);
  const [searchQuery, setSearchQuery] = useState("");

  // Функция для выполнения поиска в Supabase
  const fetchSearchResults = async (pageNumber) => {
    try {
      setIsLoading(true);

      // Декодируем параметр запроса
      let decodedQuery;
      try {
        decodedQuery = decodeURIComponent(query).trim().toLowerCase();
      } catch (error) {
        console.error("Invalid query parameter:", query);
        navigate("/");
        return;
      }

      setSearchQuery(decodedQuery);

      if (!decodedQuery) {
        setResults([]);
        setIsLoading(false);
        return;
      }

      // Разбиваем запрос на отдельные слова
      const searchTerms = decodedQuery.split(" ").filter(Boolean);

      // Создаем базовый запрос
      let queryBuilder = supabase
        .from("games")
        .select("*")
        .order("created_at", { ascending: false })
        .range(pageNumber * 24, (pageNumber + 1) * 24 - 1);

      // Добавляем условия для каждого слова
      searchTerms.forEach((term) => {
        queryBuilder = queryBuilder.or(`title.ilike.%${term}%`);
      });

      // Выполняем запрос
      const { data: newResults, error } = await queryBuilder;

      if (error) throw error;

      // Фильтруем результаты на клиенте для точного соответствия
      const filteredResults = newResults.filter((game) =>
        searchTerms.every((term) => game.title?.toLowerCase().includes(term))
      );

      if (filteredResults.length < 24) {
        setHasMore(false);
      }

      if (pageNumber === 0) {
        setResults(filteredResults);
      } else {
        setResults((prev) => [...prev, ...filteredResults]);
      }
    } catch (error) {
      console.error("Search error:", error);
    } finally {
      setIsLoading(false);
    }
  };

  // Инициализация поиска при изменении параметра query
  useEffect(() => {
    setPage(0);
    setHasMore(true);
    fetchSearchResults(0);
  }, [query]);

  // Загрузка следующей страницы результатов
  const loadMore = () => {
    if (!isLoading) {
      setPage((prev) => prev + 1);
    }
  };

  // Загрузка следующей страницы при изменении page
  useEffect(() => {
    if (page > 0) {
      fetchSearchResults(page);
    }
  }, [page]);

  // Отображение загрузчика при первом запросе
  if (isLoading && page === 0) {
    return <Loader />;
  }

  return (
    <div className="app search">
      <section className="games-list-section">
        <div className="header">
          <BackButton className="button button--back" />
          <h2 className="games-list-title games-list-title--inner-page">
            Вы искали: <br />«{searchQuery}»
          </h2>
        </div>
        <ul className="games-list games-list--vertical games-list--inner">
          {results.length > 0 ? (
            results.map((game) => (
              <GameCard
                key={game.idGame}
                game={game}
                onClick={() =>
                  navigate(`/game/${encodeURIComponent(game.gameUrl)}`)
                }
                className="game-card game-card--inner-page"
              />
            ))
          ) : (
            <div className="search__empty">
              <img src="icons/icon-empty.svg" alt="Ничего не найдено" />
              <p>Ничего не найдено.</p>
              <p>
                Измените поисковый запрос или отправьте запрос на добавление
                игры в каталог.
              </p>
            </div>
          )}
        </ul>
        {hasMore && results.length >= 24 && (
          <button
            onClick={loadMore}
            className="button button--load-more"
            disabled={isLoading}
          >
            {isLoading ? "Загружаю..." : "Загрузить ещё"}
          </button>
        )}
      </section>
    </div>
  );
};

export default SearchResults;
