import React, { useCallback, useState } from "react";
import { supabase } from "../utils/supabaseClient";

const AddGamesTab = React.memo(
  ({
    gameUrls,
    setGameUrls,
    collections,
    selectedCollection,
    setSelectedCollection,
    newCollectionTitle,
    setNewCollectionTitle,
    // Удаляем handleSubmit из пропсов, так как используем свою реализацию
    // Удаляем isSubmitting из пропсов, будем использовать локальное состояние
    gameMessage: propGameMessage,
    setGameMessage: propSetGameMessage,
    setGameMessageType: propSetGameMessageType,
  }) => {
    const [isParsing, setIsParsing] = useState(false);
    const [isSubmitting, setIsSubmitting] = useState(false); // Локальное состояние для isSubmitting
    const [localGameMessage, setLocalGameMessage] = useState("");
    const [localGameMessageType, setLocalGameMessageType] = useState("");

    // Используем переданные пропсы или локальные состояния
    const gameMessage =
      propGameMessage !== undefined ? propGameMessage : localGameMessage;
    const setGameMessage =
      propSetGameMessage !== undefined
        ? propSetGameMessage
        : setLocalGameMessage;
    const setGameMessageType =
      propSetGameMessageType !== undefined
        ? propSetGameMessageType
        : setLocalGameMessageType;

    // Функция для извлечения ID игры из URL
    const extractIdFromUrl = (url) => {
      if (!url) return null;
      const parts = url.split("/");
      return parts[parts.length - 1];
    };

    // Функция для очистки названия продукта
    const cleanProductName = (name) => {
      if (!name) return "";
      return name.replace(/\s+/g, " ").replace(/™|®|©/g, "").trim();
    };

    // Функция для парсинга игры через CORS-прокси
    const parseGame = async (gameUrl) => {
      setIsParsing(true);
      try {
        const proxyUrl = `https://cors-anywhere.herokuapp.com/${gameUrl}`;
        const response = await fetch(proxyUrl, {
          headers: {
            "X-Requested-With": "XMLHttpRequest",
          },
        });

        // Проверяем тип содержимого
        const contentType = response.headers.get("content-type");
        if (!contentType || !contentType.includes("application/json")) {
          // Если ответ не JSON, получаем текст и пробуем распарсить вручную
          const html = await response.text();
          const parser = new DOMParser();
          const doc = parser.parseFromString(html, "text/html");

          // Проверяем, не вернулась ли страница ошибки
          if (doc.querySelector("title")?.textContent.includes("Error")) {
            throw new Error("Proxy server returned an error page");
          }

          const title = cleanProductName(
            doc.querySelector('h1[data-qa="mfe-game-title#name"]')
              ?.textContent || ""
          );

          const price =
            doc
              .querySelector('span[data-qa*="finalPrice"]')
              ?.textContent?.trim() || "";

          const cover =
            doc.querySelector(
              'img[data-qa="gameBackgroundImage#heroImage#image-no-js"]'
            )?.src ||
            doc.querySelector(
              'img[data-qa="gameBackgroundImage#tileImage#image-no-js"]'
            )?.src ||
            "";

          const idGame = extractIdFromUrl(gameUrl);

          return {
            idGame,
            title,
            price,
            cover,
            gameUrl,
          };
        }
        return null;
      } catch (error) {
        console.error("Error parsing game:", error);
        return null;
      } finally {
        setIsParsing(false);
      }
    };

    // Функция для сохранения игры в Supabase
    const saveGameToCollection = async (gameData, collectionTitle) => {
      try {
        const { data, error } = await supabase.from("games").upsert(
          {
            idGame: gameData.idGame,
            title: gameData.title,
            price: gameData.price,
            cover: gameData.cover,
            gameUrl: gameData.gameUrl,
            collection_title: collectionTitle,
            created_at: new Date().toISOString(),
          },
          { onConflict: "idGame" }
        );

        if (error) throw error;
        return data;
      } catch (error) {
        console.error("Error saving game:", error);
        throw error;
      }
    };

    // Функция для создания новой коллекции
    const createCollection = async (title) => {
      try {
        const { data: maxOrderData, error: maxOrderError } = await supabase
          .from("collections")
          .select("order")
          .order("order", { ascending: false })
          .limit(1)
          .single();

        if (maxOrderError && maxOrderError.code !== "PGRST116") {
          throw maxOrderError;
        }

        const newOrder = (maxOrderData?.order || 0) + 1;

        const { data, error } = await supabase
          .from("collections")
          .insert([
            {
              title: title,
              order: newOrder,
            },
          ])
          .select()
          .single();

        if (error) throw error;
        return data;
      } catch (error) {
        console.error("Error creating collection:", error);
        throw error;
      }
    };

    // Модифицированная функция handleSubmit
    const handleSubmitWithParsing = useCallback(
      async (e) => {
        e.preventDefault();

        const finalCollectionTitle = newCollectionTitle || selectedCollection;
        if (!finalCollectionTitle) {
          setGameMessage("Выберите или создайте коллекцию");
          setGameMessageType("error");
          return;
        }

        const validUrls = gameUrls.filter(
          (url) =>
            url.includes("store.playstation.com") && url.startsWith("http")
        );

        if (validUrls.length === 0) {
          setGameMessage(
            "Добавьте хотя бы одну валидную ссылку на игру из PS Store"
          );
          setGameMessageType("error");
          return;
        }

        setIsSubmitting(true); // Теперь это точно функция
        setGameMessage("");

        try {
          if (newCollectionTitle) {
            await createCollection(newCollectionTitle);
          }

          let successCount = 0;
          for (const url of validUrls) {
            try {
              const gameData = await parseGame(url);
              if (gameData) {
                await saveGameToCollection(gameData, finalCollectionTitle);
                successCount++;
              }
            } catch (error) {
              console.error(`Error processing game ${url}:`, error);
            }
          }

          if (successCount > 0) {
            setGameMessage(
              `Успешно добавлено ${successCount} игр в коллекцию "${finalCollectionTitle}"`
            );
            setGameMessageType("success");
            setGameUrls([""]);
            setNewCollectionTitle("");
          } else {
            setGameMessage("Не удалось добавить ни одной игры");
            setGameMessageType("error");
          }
        } catch (error) {
          console.error("Error saving games:", error);
          setGameMessage("Ошибка при сохранении игр в коллекцию");
          setGameMessageType("error");
        } finally {
          setIsSubmitting(false); // Корректное использование функции
        }
      },
      [
        gameUrls,
        selectedCollection,
        newCollectionTitle,
        setGameUrls,
        setNewCollectionTitle,
        setGameMessage,
        setGameMessageType,
      ]
    );

    const handleGameUrlChange = useCallback(
      (index, value) => {
        setGameUrls((prevUrls) => {
          const newUrls = [...prevUrls];
          newUrls[index] = value;
          return newUrls;
        });
      },
      [setGameUrls]
    );

    const addGameUrlField = useCallback(() => {
      setGameUrls((prevUrls) => [...prevUrls, ""]);
    }, [setGameUrls]);

    return (
      <form onSubmit={handleSubmitWithParsing} className="form form--add-game">
        <fieldset className="form__fieldset">
          <legend>Добавить игру в коллекцию*</legend>

          <div className="form__wrapper">
            {gameUrls.map((url, index) => (
              <div key={index} className="form__wrapper-input">
                <label
                  htmlFor={`gameUrl-${index}`}
                  className="form__label"
                ></label>
                <input
                  type="text"
                  id={`gameUrl-${index}`}
                  name={`gameUrl-${index}`}
                  className="input form__input"
                  value={url}
                  onChange={(e) => handleGameUrlChange(index, e.target.value)}
                  required={index === 0}
                  placeholder="Ссылка на игру из PS Store"
                />
                {isParsing && index === gameUrls.length - 1 && (
                  <span className="form__parsing-indicator">Парсинг...</span>
                )}
              </div>
            ))}
            <button
              type="button"
              className="button form__button form__button-add"
              onClick={addGameUrlField}
              disabled={gameUrls.length >= 10 || isParsing}
            >
              + Добавить ещё игру
            </button>
            <p className="form__advice">* До 10 игр за раз</p>
          </div>

          <div className="form__wrapper">
            <select
              id="collectionSelect"
              className="input form__input form__select"
              value={selectedCollection}
              onChange={(e) => setSelectedCollection(e.target.value)}
              disabled={isParsing}
            >
              <option value="" disabled>
                Выберите коллекцию
              </option>
              {collections.map((collection) => (
                <option key={collection.title} value={collection.title}>
                  {collection.title}
                </option>
              ))}
            </select>

            <input
              type="text"
              id="newCollectionTitle"
              name="newCollectionTitle"
              className="input form__input"
              value={newCollectionTitle}
              onChange={(e) => setNewCollectionTitle(e.target.value)}
              placeholder="Название новой коллекции"
              disabled={isParsing}
            />
          </div>

          <button
            type="submit"
            className="button form__button"
            disabled={isSubmitting || isParsing}
          >
            {isSubmitting ? "Добавляю игры..." : "Добавить игры"}
          </button>
        </fieldset>
        <p className="form__advice">
          * Ссылка на игру из PS Store должна быть вида <br></br>
          https://store.playstation.com/en-tr/product/EP9000-PPSA05684_00-UNCHARTED4R00000
        </p>
        {gameMessage && (
          <p className={`form__message form__message--${localGameMessageType}`}>
            {gameMessage}
          </p>
        )}
      </form>
    );
  }
);

export default AddGamesTab;
