import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import BackButton from "../components/BackButton";

function CartPage() {
  const [cartItems, setCartItems] = useState([]);
  const [total, setTotal] = useState(0);

  useEffect(() => {
    const cart = JSON.parse(localStorage.getItem("cart")) || [];
    setCartItems(cart);

    const calculatedTotal = cart.reduce((sum, item) => {
      const price = parseFloat(item.price) || 0;
      return sum + price;
    }, 0);
    setTotal(calculatedTotal);
  }, []);

  const removeFromCart = (gameUrl) => {
    const updatedCart = cartItems.filter((item) => item.gameUrl !== gameUrl);
    setCartItems(updatedCart);
    localStorage.setItem("cart", JSON.stringify(updatedCart));

    const calculatedTotal = updatedCart.reduce((sum, item) => {
      const price = parseFloat(item.price) || 0;
      return sum + price;
    }, 0);
    setTotal(calculatedTotal);
  };

  const clearCart = () => {
    setCartItems([]);
    localStorage.setItem("cart", JSON.stringify([]));
    setTotal(0);
  };

  const getCleanLink = (url) => {
    if (!url) return "#";
    return url.replace(/(#+)/g, "#");
  };

  return (
    <div className="app app--cart">
      <div className="header">
        <BackButton />
        <h2 className="games-list-title games-list-title--inner-page">
          Корзина
        </h2>
      </div>
      <ul className="cart-list">
        {cartItems.length === 0 ? (
          <div className="search__empty">
            <img src="icons/icon-bag-empty.svg" alt="Ничего не найдено" width="80" />
            <p>Ваша корзина пуста.</p>
            <p>Продолжайте выбирать игры или вернитесь на главную страницу.</p>
            <Link
              to="/"
              className="button"
              title="На главную"
              style={{marginTop: "24px"}}
            >На главную</Link>
          </div>
        ) : (
          <>
            {cartItems.map((item, index) => (
              <li key={index} className="cart-list__item">
                <div className="cart-list__img-wrapper">
                  <img
                    src={item.cover}
                    alt={item.title}
                    className="game-cover"
                  />
                </div>
                <div className="cart-list__info-game">
                  <Link
                    to={getCleanLink(item.url)}
                    className="cart-list__title"
                  >
                    {item.title}
                  </Link>
                  {item.edition && (
                    <span className="cart-list__edition">{item.edition}</span>
                  )}
                  <span className="cart-list__platforms">{item.platforms}</span>
                  <p className="cart-list__price">{item.price}</p>
                </div>
                <button
                  onClick={() => removeFromCart(item.gameUrl)}
                  className="button button--remove cart-list__delete"
                  title="Удалить из корзины"
                ></button>
              </li>
            ))}
            {/* <button onClick={clearCart} className="button button--clear">
              Очистить корзину
            </button> */}
            <div>Итого: {total.toFixed(2)} ₽</div>
            <div className="cart-list__message">
              <p>
                &#x26A0; Конечные цены могут отличаться, уточняйте у менеджера.
              </p>
            </div>
            <button className="button button--checkout">
              Передать заказ в обработку 📨
            </button>
          </>
        )}
      </ul>
    </div>
  );
}

export default CartPage;
