import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { supabase } from "../utils/supabaseClient";
import Loader from "../components/Loader";
import BackButton from "../components/BackButton";
import GameCard from "../components/GameCard";

function AllGamesPage() {
  const navigate = useNavigate();
  const [games, setGames] = useState([]);
  const [page, setPage] = useState(0);
  const [hasMore, setHasMore] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isInitialLoading, setIsInitialLoading] = useState(true);

  // Состояния для фильтров
  const [sortBy, setSortBy] = useState("-created_at");
  const [filterPlatform, setFilterPlatform] = useState("");
  const [filterCollection, setFilterCollection] = useState("");
  const [availablePlatforms, setAvailablePlatforms] = useState([]);
  const [availableCollections, setAvailableCollections] = useState([]);

  useEffect(() => {
    // Загружаем уникальные платформы и коллекции для фильтров
    const fetchFilterOptions = async () => {
      const { data: platforms } = await supabase
        .from("games")
        .select("platforms")
        .not("platforms", "is", null);

      const { data: collections } = await supabase
        .from("collections")
        .select("title");

      setAvailablePlatforms(
        [...new Set(platforms.flatMap((g) => g.platforms))].sort()
      );

      setAvailableCollections(collections.map((c) => c.title).sort());
    };

    fetchFilterOptions();
  }, []);

  useEffect(() => {
    const fetchGames = async () => {
      try {
        setIsLoading(true);

        // Определяем параметры сортировки
        const [column, order] = sortBy.startsWith("-")
          ? [sortBy.substring(1), false]
          : [sortBy, true];

        // Строим запрос с учетом фильтров
        let query = supabase
          .from("games")
          .select("*")
          .order(column, { ascending: order })
          .range(page * 24, (page + 1) * 24 - 1);

        if (filterPlatform) {
          query = query.contains("platforms", [filterPlatform]);
        }

        if (filterCollection) {
          query = query.eq("collection_title", filterCollection);
        }

        const { data: newGames, error } = await query;

        if (error) throw error;

        setGames((prevGames) => {
          const uniqueGames = [...prevGames, ...newGames].filter(
            (game, index, self) =>
              index === self.findIndex((g) => g.title === game.title)
          );
          return uniqueGames;
        });

        setHasMore(newGames.length === 24);
      } catch (error) {
        console.error("Error fetching all games:", error);
      } finally {
        setIsLoading(false);
        setIsInitialLoading(false);
      }
    };

    fetchGames();
  }, [page, sortBy, filterPlatform, filterCollection]);

  const loadMore = () => {
    if (!isLoading) {
      setPage((prevPage) => prevPage + 1);
    }
  };

  const resetFilters = () => {
    setSortBy("-created_at");
    setFilterPlatform("");
    setFilterCollection("");
    setPage(0);
    setGames([]);
  };

  return (
    <div className="app">
      {isInitialLoading ? (
        <Loader />
      ) : (
        <section className="games-list-section">
          <div className="header">
            <BackButton />
            <h2 className="games-list-title games-list-title--inner-page">
              Все игры
            </h2>
          </div>

          {/* Фильтры и сортировка */}
          {/* <div className="filters-container  form__filers">
            <div className="filter-group">
              <label htmlFor="sort-by">Сортировка:</label>
              <select
                id="sort-by"
                className="filter-select form__input form__select"
                value={sortBy}
                onChange={(e) => {
                  setSortBy(e.target.value);
                  setPage(0);
                  setGames([]);
                }}
              >
                <option value="-created_at">Сначала новые</option>
                <option value="created_at">Сначала старые</option>
                <option value="title">По названию (А-Я)</option>
                <option value="-title">По названию (Я-А)</option>
                <option value="convertedPrice">По цене (↑)</option>
                <option value="-convertedPrice">По цене (↓)</option>
                <option value="release_date">По дате выхода (↑)</option>
                <option value="-release_date">По дате выхода (↓)</option>
              </select>
            </div>

            <div className="filter-group">
              <label htmlFor="filter-platform">Платформа:</label>
              <select
                id="filter-platform"
                className="filter-select form__input form__select"
                value={filterPlatform}
                onChange={(e) => {
                  setFilterPlatform(e.target.value);
                  setPage(0);
                  setGames([]);
                }}
              >
                <option value="">Все платформы</option>
                {availablePlatforms.map((platform) => (
                  <option key={platform} value={platform}>
                    {platform}
                  </option>
                ))}
              </select>
            </div>

            <div className="filter-group">
              <label htmlFor="filter-collection">Коллекция:</label>
              <select
                id="filter-collection"
                className="filter-select form__input form__select"
                value={filterCollection}
                onChange={(e) => {
                  setFilterCollection(e.target.value);
                  setPage(0);
                  setGames([]);
                }}
              >
                <option value="">Все коллекции</option>
                {availableCollections.map((collection) => (
                  <option key={collection} value={collection}>
                    {collection}
                  </option>
                ))}
              </select>
            </div>

            <button className="reset-filters-button" onClick={resetFilters}>
              Сбросить фильтры
            </button>
          </div> */}

          <ul className="games-list games-list--vertical games-list--inner">
            {games.map((game, index) => (
              <GameCard
                className="game-card--inner-page"
                key={`${game.idGame}-${index}`}
                game={game}
                onClick={() =>
                  navigate(`/game/${encodeURIComponent(game.gameUrl)}`)
                }
              />
            ))}
          </ul>

          {hasMore && games.length >= 24 && (
            <button
              onClick={loadMore}
              className="button button--load-more"
              disabled={isLoading}
            >
              {isLoading ? "Загружаю..." : "Загрузить ещё"}
            </button>
          )}
        </section>
      )}
    </div>
  );
}

export default AllGamesPage;
