import React, { useState, useEffect } from "react";
import { supabase } from "../utils/supabaseClient";

const SubscriptionsTab = () => {
  const [subscriptions, setSubscriptions] = useState([]);
  const [editingSubscriptions, setEditingSubscriptions] = useState([]);
  const [selectedSubscriptionId, setSelectedSubscriptionId] = useState(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [message, setMessage] = useState("");
  const [messageType, setMessageType] = useState("");

  // Загрузка подписок при монтировании компонента
  useEffect(() => {
    const loadSubscriptions = async () => {
      try {
        const { data, error } = await supabase
          .from("subscriptions")
          .select("*")
          .order("id");

        if (error) throw error;

        setSubscriptions(data || []);
        setEditingSubscriptions(data || []);
        if (data?.length > 0) {
          setSelectedSubscriptionId(data[0].id);
        }
      } catch (error) {
        console.error("Ошибка при загрузке подписок:", error);
        showMessage("Не удалось загрузить подписки", "error");
      }
    };

    loadSubscriptions();
  }, []);

  const showMessage = (text, type = "success") => {
    setMessage(text);
    setMessageType(type);
    setTimeout(() => {
      setMessage("");
      setMessageType("");
    }, 3000);
  };

  const handleInputChange = (field, value) => {
    const updatedSubscriptions = editingSubscriptions.map((subscription) => {
      if (subscription.id === selectedSubscriptionId) {
        return {
          ...subscription,
          [field]: value,
        };
      }
      return subscription;
    });
    setEditingSubscriptions(updatedSubscriptions);
  };

  const handleDurationChange = (index, value) => {
    const updatedSubscriptions = editingSubscriptions.map((subscription) => {
      if (subscription.id === selectedSubscriptionId) {
        const updatedDurations = [...subscription.durations];
        updatedDurations[index].price = value;
        return {
          ...subscription,
          durations: updatedDurations,
        };
      }
      return subscription;
    });
    setEditingSubscriptions(updatedSubscriptions);
  };

  const handleUpdateAllSubscriptions = async () => {
    setIsSubmitting(true);
    try {
      // Обновляем все подписки в базе данных
      const updates = editingSubscriptions.map(async (subscription) => {
        const { error } = await supabase
          .from("subscriptions")
          .update({
            title: subscription.title,
            cover: subscription.cover,
            durations: subscription.durations,
            description: subscription.description,
          })
          .eq("id", subscription.id);

        if (error) throw error;
      });

      await Promise.all(updates);
      showMessage("Подписки успешно обновлены");
    } catch (error) {
      console.error("Ошибка при обновлении подписок:", error);
      showMessage("Не удалось обновить подписки", "error");
    } finally {
      setIsSubmitting(false);
    }
  };

  const selectedSubscription = editingSubscriptions.find(
    (sub) => sub.id === selectedSubscriptionId
  );

  return (
    <div>
      {/* Сообщение об успешном обновлении или ошибке */}
      {message && (
        <p className={`form__message form__message--${messageType}`}>
          {message}
        </p>
      )}

      {/* Кнопки для переключения между подписками */}
      <div className="form__subscriptions-list">
        {subscriptions.map((subscription) => (
          <button
            key={subscription.id}
            className={`button form__button form__button--sub ${
              selectedSubscriptionId === subscription.id
                ? "form__button--selected"
                : ""
            }`}
            onClick={() => setSelectedSubscriptionId(subscription.id)}
          >
            {subscription.title}
          </button>
        ))}
      </div>

      {/* Форма для редактирования выбранной подписки */}
      {selectedSubscription && (
        <div className="form form--subs">
          <fieldset className="form__fieldset">
            <legend>Редактировать подписку</legend>

            {/* Поле для редактирования названия подписки */}
            <div className="form__wrapper">
              <label
                htmlFor={`title-${selectedSubscription.id}`}
                className="form__label"
              >
                Название
              </label>
              <input
                type="text"
                id={`title-${selectedSubscription.id}`}
                name="title"
                className="input form__input"
                value={selectedSubscription.title || ""}
                onChange={(e) => handleInputChange("title", e.target.value)}
              />
            </div>

            {/* Поле для редактирования обложки подписки */}
            <div className="form__wrapper">
              <label
                htmlFor={`cover-${selectedSubscription.id}`}
                className="form__label"
              >
                Обложка
              </label>
              <input
                type="text"
                id={`cover-${selectedSubscription.id}`}
                name="cover"
                className="input form__input"
                value={selectedSubscription.cover || ""}
                onChange={(e) => handleInputChange("cover", e.target.value)}
              />
            </div>

            {/* Поля для редактирования цен длительностей подписки */}
            <div className="form__wrapper form__durations">
              {(selectedSubscription.durations || []).map((duration, index) => (
                <div key={index} className="form__wrapper">
                  <label
                    htmlFor={`duration-${selectedSubscription.id}-${index}`}
                    className="form__label"
                  >
                    {duration.duration}
                  </label>
                  <input
                    type="text"
                    id={`duration-${selectedSubscription.id}-${index}`}
                    className="input form__input"
                    value={duration.price || ""}
                    onChange={(e) =>
                      handleDurationChange(index, e.target.value)
                    }
                    placeholder={`Цена для ${duration.duration}`}
                  />
                </div>
              ))}
            </div>

            {/* Поле для редактирования описания подписки */}
            <div className="form__wrapper">
              <label
                htmlFor={`description-${selectedSubscription.id}`}
                className="form__label"
              >
                Описание
              </label>
              <textarea
                id={`description-${selectedSubscription.id}`}
                name="description"
                className="input form__input form__textarea"
                value={selectedSubscription.description || ""}
                onChange={(e) =>
                  handleInputChange("description", e.target.value)
                }
              />
            </div>

            {/* Кнопка для сохранения всех подписок */}
            <button
              type="button"
              className="button form__button"
              onClick={handleUpdateAllSubscriptions}
              disabled={isSubmitting}
            >
              {isSubmitting ? "Обновляю подписки..." : "Обновить подписки"}
            </button>
          </fieldset>
        </div>
      )}
    </div>
  );
};

export default SubscriptionsTab;
