const NodeCache = require("node-cache");
const cache = new NodeCache({ stdTTL: 3600 }); // Кэш на 1 час

const { createClient } = require("@supabase/supabase-js");
const supabaseUrl =
  process.env.REACT_APP_SUPABASE_URL ||
  "https://szgdtzsnoappxgmrhtub.supabase.co";
  const supabaseKey =
  process.env.REACT_APP_SUPABASE_KEY ||
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6InN6Z2R0enNub2FwcHhnbXJodHViIiwicm9sZSI6ImFub24iLCJpYXQiOjE3Mzk4ODQ0MjQsImV4cCI6MjA1NTQ2MDQyNH0.NE628c1l1YO3xK6GOF_ppz1mjnE1t-VfZj1ILmKloZw";
const supabase = createClient(supabaseUrl, supabaseKey);

// Основные функции:
async function getExchangeRate() {
  const cachedRate = cache.get("exchange_rate");
  if (cachedRate) return cachedRate;

  const { data, error } = await supabase
    .from("coefficients")
    .select("value")
    .eq("title", "exchange_rate")
    .single();

  if (error || !data?.value) {
    console.error(
      "Ошибка получения курса:",
      error?.message || "Курс не найден"
    );
    return 2.5; // Фолбэк значение
  }

  cache.set("exchange_rate", data.value);
  return data.value;
}

function parsePrice(priceStr) {
  if (!priceStr) return null;
  const cleanStr = priceStr.replace(/[^\d,]/g, "").replace(",", ".");
  return parseFloat(cleanStr) || null;
}

async function convertToRub(priceInTry) {
  if (!priceInTry) return null;

  const tryRate = await getExchangeRate();
  const numericPrice = parsePrice(priceInTry);

  return numericPrice ? (numericPrice * tryRate).toFixed(2) : null;
}

// Для использования в компонентах:
async function getFormattedPrice(priceInTry) {
  const rubPrice = await convertToRub(priceInTry);
  return rubPrice ? `${rubPrice} ₽` : "Цена недоступна";
}

module.exports = {
  getExchangeRate,
  parsePrice,
  convertToRub,
  getFormattedPrice,
};
