import React, { useState, useEffect } from "react";
import { supabase } from "../utils/supabaseClient";
import Dialog from "./Dialog";

const AllGamesTab = ({ collections }) => {
  const [games, setGames] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [sortBy, setSortBy] = useState("-created_at");
  const [filterCollection, setFilterCollection] = useState("");
  const [editingGame, setEditingGame] = useState(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [gameMessage, setGameMessage] = useState("");
  const [gameMessageType, setGameMessageType] = useState("");
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [gameToDelete, setGameToDelete] = useState(null);

  // Загрузка игр
  const loadGames = async () => {
    try {
      let query = supabase.from("games").select("*");

      // Применяем сортировку
      if (sortBy) {
        const [column, order] = sortBy.startsWith("-")
          ? [sortBy.substring(1), false]
          : [sortBy, true];
        query = query.order(column, { ascending: order });
      }

      const { data, error } = await query;

      if (error) throw error;
      setGames(data || []);
    } catch (error) {
      console.error("Ошибка при загрузке игр:", error);
      showMessage("Не удалось загрузить игры", "error");
    }
  };

  useEffect(() => {
    loadGames();
  }, [sortBy]);

  const showMessage = (message, type = "success") => {
    setGameMessage(message);
    setGameMessageType(type);
    setTimeout(() => {
      setGameMessage("");
      setGameMessageType("");
    }, 3000);
  };

  const openDeleteDialog = (game) => {
    if (game && game.title) {
      setGameToDelete(game);
      setIsDialogOpen(true);
    } else {
      console.error("Ошибка: объект игры не содержит названия (title)", game);
    }
  };

  const closeDeleteDialog = () => {
    setIsDialogOpen(false);
    setGameToDelete(null);
  };

  const handleDeleteGame = async (gameId) => {
    try {
      const { error } = await supabase.from("games").delete().eq("id", gameId);

      if (error) throw error;

      showMessage("Игра успешно удалена");
      loadGames();
    } catch (error) {
      console.error("Ошибка при удалении игры:", error);
      showMessage("Не удалось удалить игру", "error");
    }
  };

  const handleConfirmDelete = async () => {
    if (gameToDelete && gameToDelete.id) {
      await handleDeleteGame(gameToDelete.id);
      closeDeleteDialog();
    }
  };

  const handleEditGameClick = (game) => {
    if (editingGame?.id === game.id) {
      setEditingGame(null);
    } else {
      setEditingGame({
        ...game,
        cover: game.cover || "",
        saved_cover: game.saved_cover || "",
        editions:
          game.editions?.map((edition) => ({
            ...edition,
            saved_cover: edition.saved_cover || "",
          })) || [],
        addons:
          game.addons?.map((addon) => ({
            ...addon,
            saved_cover: addon.saved_cover || "",
          })) || [],
        platforms: Array.isArray(game.platforms)
          ? game.platforms.join(", ")
          : game.platforms || "",
        tags: Array.isArray(game.tags) ? game.tags.join(", ") : game.tags || "",
        formattedDate: game.release_date
          ? new Date(game.release_date).toLocaleDateString("ru-RU")
          : "",
      });
    }
  };

  const handleGameDateChange = (e) => {
    const dateValue = e.target.value;
    setEditingGame({
      ...editingGame,
      release_date: dateValue,
      formattedDate: dateValue
        ? new Date(dateValue).toLocaleDateString("ru-RU")
        : "",
    });
  };

  const handleAddonDateChange = (e, addonIndex) => {
    const dateValue = e.target.value;
    const updatedAddons = [...editingGame.addons];
    updatedAddons[addonIndex] = {
      ...updatedAddons[addonIndex],
      release_date: dateValue,
      formattedDate: dateValue
        ? new Date(dateValue).toLocaleDateString("ru-RU")
        : "",
    };
    setEditingGame({
      ...editingGame,
      addons: updatedAddons,
    });
  };

  const handleUpdateGame = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      // Функция для безопасного преобразования строки в массив
      const safeStringToArray = (str) => {
        if (!str) return [];
        if (Array.isArray(str)) return str;
        return str
          .split(",")
          .map((item) => item.trim())
          .filter((item) => item);
      };

      // Подготовка данных для обновления
      const updatedGame = {
        ...editingGame,
        platforms: safeStringToArray(editingGame.platforms),
        tags: safeStringToArray(editingGame.tags),
        editions:
          editingGame.editions?.map((edition) => ({
            ...edition,
            platforms: safeStringToArray(edition.platforms),
            tags: safeStringToArray(edition.tags),
            saved_cover: edition.saved_cover || "",
          })) || [],
        addons:
          editingGame.addons?.map((addon) => ({
            ...addon,
            platforms: safeStringToArray(addon.platforms),
            tags: safeStringToArray(addon.tags),
            saved_cover: addon.saved_cover || "",
          })) || [],
      };

      // Обновляем игру в базе данных
      const { error } = await supabase
        .from("games")
        .update(updatedGame)
        .eq("id", editingGame.id);

      if (error) throw error;

      showMessage("Игра успешно обновлена");
      loadGames();
      setEditingGame(null);
    } catch (error) {
      console.error("Ошибка при обновлении игры:", error);
      showMessage("Не удалось обновить игру", "error");
    } finally {
      setIsSubmitting(false);
    }
  };

  const filteredGames = games.filter((game) => {
    // Фильтр по поисковому запросу
    if (
      searchQuery &&
      !game.title.toLowerCase().includes(searchQuery.toLowerCase())
    ) {
      return false;
    }
    // Фильтр по коллекции
    if (filterCollection && game.collection_title !== filterCollection) {
      return false;
    }
    return true;
  });

  return (
    <div>
      <div className="form__filters">
        <input
          type="text"
          placeholder="Поиск по названию"
          className="input form__input"
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
        />
        <select
          className="input form__input form__select"
          value={sortBy}
          onChange={(e) => setSortBy(e.target.value)}
        >
          <option value="-created_at">Сначала новые</option>
          <option value="created_at">Сначала старые</option>
          <option value="title">По названию (А-Я)</option>
          <option value="-title">По названию (Я-А)</option>
        </select>
        <select
          className="input form__input form__select"
          value={filterCollection}
          onChange={(e) => setFilterCollection(e.target.value)}
        >
          <option value="">Все коллекции</option>
          {collections.map((collection) => (
            <option key={collection.title} value={collection.title}>
              {collection.title}
            </option>
          ))}
        </select>
      </div>

      <div className="form__games-list">
        {filteredGames.map((game) => (
          <div key={game.id} className="form__game-item">
            <button
              className={`button form__button form__button--edit ${
                editingGame?.id === game.id ? "form__button--selected" : ""
              }`}
              onClick={() => handleEditGameClick(game)}
            >
              {game.title}
            </button>

            {editingGame?.id === game.id && (
              <form onSubmit={handleUpdateGame} className="form form--games">
                <fieldset className="form__fieldset">
                  <legend>Редактировать игру</legend>

                  {/* Основная информация об игре */}
                  <div className="form__wrapper">
                    <label
                      htmlFor={`game-title-${game.id}`}
                      className="form__label"
                    >
                      Название игры
                    </label>
                    <input
                      type="text"
                      id={`game-title-${game.id}`}
                      name="title"
                      className="input form__input"
                      value={editingGame.title || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          title: e.target.value,
                        })
                      }
                      required
                    />
                  </div>

                  <div className="form__wrapper">
                    <label
                      htmlFor={`game-collection-${game.id}`}
                      className="form__label"
                    >
                      Коллекция
                    </label>
                    <select
                      id={`game-collection-${game.id}`}
                      name="collection_title"
                      className="input form__input form__select"
                      value={editingGame.collection_title || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          collection_title: e.target.value,
                        })
                      }
                    >
                      <option value="">Без коллекции</option>
                      {collections.map((collection) => (
                        <option key={collection.title} value={collection.title}>
                          {collection.title}
                        </option>
                      ))}
                    </select>
                  </div>

                  {/* Остальные поля с исправленными id и for */}
                  <div className="form__wrapper">
                    <label
                      htmlFor={`game-price-${game.id}`}
                      className="form__label"
                    >
                      Цена игры в лирах
                    </label>
                    <input
                      type="text"
                      id={`game-price-${game.id}`}
                      className="input form__input"
                      value={editingGame.price || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          price: e.target.value,
                        })
                      }
                      placeholder="1.399,00 TL"
                    />
                  </div>

                  <div className="form__wrapper">
                    <label
                      htmlFor={`convertedPrice-${game.id}`}
                      className="form__label"
                    >
                      Цена игры в рублях
                    </label>
                    <input
                      type="text"
                      id={`convertedPrice-${game.id}`}
                      className="input form__input"
                      value={editingGame.convertedPrice || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          convertedPrice: e.target.value,
                        })
                      }
                      placeholder="5.999 ₽"
                    />
                  </div>

                  <div className="form__wrapper">
                    <label
                      htmlFor={`price_rub-${game.id}`}
                      className="form__label"
                    >
                      Индивидуальная цена в рублях*
                    </label>
                    <input
                      type="text"
                      id={`price_rub-${game.id}`}
                      className="input form__input"
                      value={editingGame.price_rub || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          price_rub: e.target.value,
                        })
                      }
                      placeholder="5.999 ₽"
                    />
                  </div>

                  <div className="form__wrapper">
                    <label
                      htmlFor={`game-discount-${game.id}`}
                      className="form__label"
                    >
                      Скидка игры
                    </label>
                    <input
                      type="text"
                      id={`game-discount-${game.id}`}
                      className="input form__input"
                      value={editingGame.discount || ""}
                      placeholder="-50%"
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          discount: e.target.value,
                        })
                      }
                    />
                  </div>

                  <div className="form__wrapper">
                    <label htmlFor={`voice-${game.id}`} className="form__label">
                      Озвучка
                    </label>
                    <input
                      type="text"
                      id={`voice-${game.id}`}
                      className="input form__input"
                      value={editingGame.voice || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          voice: e.target.value,
                        })
                      }
                      placeholder="Русская, английская"
                    />
                  </div>

                  <div className="form__wrapper">
                    <label
                      htmlFor={`subtitles-${game.id}`}
                      className="form__label"
                    >
                      Субтитры
                    </label>
                    <input
                      type="text"
                      id={`subtitles-${game.id}`}
                      className="input form__input"
                      value={editingGame.subtitles || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          subtitles: e.target.value,
                        })
                      }
                      placeholder="Русские, английские"
                    />
                  </div>

                  <div className="form__wrapper">
                    <label
                      htmlFor={`release_date-${game.id}`}
                      className="form__label"
                    >
                      Дата выхода
                    </label>
                    <input
                      type="date"
                      id={`release_date-${game.id}`}
                      className="input form__input"
                      value={editingGame.release_date || ""}
                      onChange={handleGameDateChange}
                    />
                  </div>

                  <div className="form__wrapper">
                    <label
                      htmlFor={`gameUrl-${game.id}`}
                      className="form__label"
                    >
                      Ссылка на игру в PS Store
                    </label>
                    <input
                      type="text"
                      id={`gameUrl-${game.id}`}
                      className="input form__input"
                      value={editingGame.gameUrl || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          gameUrl: e.target.value,
                        })
                      }
                      placeholder="https://store.playstation.com/..."
                    />
                  </div>

                  <div className="form__wrapper">
                    <label
                      htmlFor={`translatedGenres-${game.id}`}
                      className="form__label"
                    >
                      Жанры (через запятую)
                    </label>
                    <input
                      type="text"
                      id={`translatedGenres-${game.id}`}
                      className="input form__input"
                      value={editingGame.translatedGenres || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          translatedGenres: e.target.value,
                        })
                      }
                      placeholder="Экшен, Приключение"
                    />
                  </div>

                  <div className="form__wrapper">
                    <label
                      htmlFor={`platforms-${game.id}`}
                      className="form__label"
                    >
                      Платформы (через запятую)
                    </label>
                    <input
                      type="text"
                      id={`platforms-${game.id}`}
                      className="input form__input"
                      value={editingGame.platforms || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          platforms: e.target.value,
                        })
                      }
                      placeholder="PS5, PS4"
                    />
                  </div>

                  <div className="form__wrapper">
                    <label htmlFor={`tags-${game.id}`} className="form__label">
                      Тэги (через запятую)
                    </label>
                    <input
                      type="text"
                      id={`tags-${game.id}`}
                      className="input form__input"
                      value={editingGame.tags || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          tags: e.target.value,
                        })
                      }
                      placeholder="Эксклюзив, Издание дня"
                    />
                  </div>

                  <div className="form__wrapper">
                    <label
                      htmlFor={`description-${game.id}`}
                      className="form__label"
                    >
                      Описание
                    </label>
                    <textarea
                      id={`description-${game.id}`}
                      className="input form__input"
                      value={editingGame.description || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          description: e.target.value,
                        })
                      }
                      rows="5"
                    />
                  </div>

                  <div className="form__wrapper">
                    <label
                      htmlFor={`cover-square-${game.id}`}
                      className="form__label"
                    >
                      URL квадратной обложки
                    </label>
                    <input
                      type="text"
                      id={`cover-square-${game.id}`}
                      className="input form__input"
                      value={editingGame.cover || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          cover: e.target.value,
                        })
                      }
                      placeholder="https://example.com/square-cover.jpg"
                    />
                  </div>

                  <div className="form__wrapper">
                    <label
                      htmlFor={`cover-main-${game.id}`}
                      className="form__label"
                    >
                      URL основной обложки
                    </label>
                    <input
                      type="text"
                      id={`cover-main-${game.id}`}
                      className="input form__input"
                      value={editingGame.saved_cover || ""}
                      onChange={(e) =>
                        setEditingGame({
                          ...editingGame,
                          saved_cover: e.target.value,
                        })
                      }
                      placeholder="https://example.com/main-cover.jpg"
                    />
                  </div>

                  {editingGame?.editions?.length > 0 && (
                    <div className="form__wrapper">
                      <h3>Издания</h3>
                      {editingGame.editions.map((edition, index) => (
                        <div key={index} className="form__edition">
                          <hr className="form__hr" />

                          <div className="form__wrapper">
                            <label
                              htmlFor={`editionTitle-${game.id}-${index}`}
                              className="form__label"
                            >
                              Название издания
                            </label>
                            <input
                              type="text"
                              id={`editionTitle-${game.id}-${index}`}
                              className="input form__input"
                              value={edition.title || ""}
                              onChange={(e) => {
                                const updatedEditions = [
                                  ...editingGame.editions,
                                ];
                                updatedEditions[index].title = e.target.value;
                                setEditingGame({
                                  ...editingGame,
                                  editions: updatedEditions,
                                });
                              }}
                            />
                          </div>

                          <div className="form__wrapper">
                            <label
                              htmlFor={`editionPrice-${game.id}-${index}`}
                              className="form__label"
                            >
                              Цена издания в лирах
                            </label>
                            <input
                              type="text"
                              id={`editionPrice-${game.id}-${index}`}
                              className="input form__input"
                              value={edition.price || ""}
                              onChange={(e) => {
                                const updatedEditions = [
                                  ...editingGame.editions,
                                ];
                                updatedEditions[index].price = e.target.value;
                                setEditingGame({
                                  ...editingGame,
                                  editions: updatedEditions,
                                });
                              }}
                              placeholder="1.399,00 TL"
                            />
                          </div>

                          <div className="form__wrapper">
                            <label
                              htmlFor={`editionDiscount-${game.id}-${index}`}
                              className="form__label"
                            >
                              Скидка издания
                            </label>
                            <input
                              type="text"
                              id={`editionDiscount-${game.id}-${index}`}
                              className="input form__input"
                              value={edition.discount || ""}
                              placeholder="-50%"
                              onChange={(e) => {
                                const updatedEditions = [
                                  ...editingGame.editions,
                                ];
                                updatedEditions[index].discount =
                                  e.target.value;
                                setEditingGame({
                                  ...editingGame,
                                  editions: updatedEditions,
                                });
                              }}
                            />
                          </div>

                          <div className="form__wrapper">
                            <label
                              htmlFor={`editionConvertedPrice-${game.id}-${index}`}
                              className="form__label"
                            >
                              Цена издания в рублях
                            </label>
                            <input
                              type="text"
                              id={`editionConvertedPrice-${game.id}-${index}`}
                              className="input form__input"
                              value={edition.convertedPrice || ""}
                              onChange={(e) => {
                                const updatedEditions = [
                                  ...editingGame.editions,
                                ];
                                updatedEditions[index].convertedPrice =
                                  e.target.value;
                                setEditingGame({
                                  ...editingGame,
                                  editions: updatedEditions,
                                });
                              }}
                              placeholder="5.999 ₽"
                            />
                          </div>

                          <div className="form__wrapper">
                            <label
                              htmlFor={`editionPriceRub-${game.id}-${index}`}
                              className="form__label"
                            >
                              Индивидуальная цена в рублях*
                            </label>
                            <input
                              type="text"
                              id={`editionPriceRub-${game.id}-${index}`}
                              className="input form__input"
                              value={edition.price_rub || ""}
                              onChange={(e) => {
                                const updatedEditions = [
                                  ...editingGame.editions,
                                ];
                                updatedEditions[index].price_rub =
                                  e.target.value;
                                setEditingGame({
                                  ...editingGame,
                                  editions: updatedEditions,
                                });
                              }}
                              placeholder="5.999 ₽"
                            />
                          </div>

                          <div className="form__wrapper">
                            <label
                              htmlFor={`editionDescription-${game.id}-${index}`}
                              className="form__label"
                            >
                              Описание издания
                            </label>
                            <textarea
                              id={`editionDescription-${game.id}-${index}`}
                              className="input form__input"
                              value={edition.description?.[0] || ""}
                              onChange={(e) => {
                                const updatedEditions = [
                                  ...editingGame.editions,
                                ];
                                updatedEditions[index].description = [
                                  e.target.value,
                                ];
                                setEditingGame({
                                  ...editingGame,
                                  editions: updatedEditions,
                                });
                              }}
                              rows="3"
                            />
                          </div>

                          <div className="form__wrapper">
                            <label
                              htmlFor={`edition-cover-${game.id}-${index}`}
                              className="form__label"
                            >
                              URL обложки издания
                            </label>
                            <input
                              type="text"
                              id={`edition-cover-${game.id}-${index}`}
                              className="input form__input"
                              value={edition.saved_cover || ""}
                              onChange={(e) => {
                                const updatedEditions = [
                                  ...editingGame.editions,
                                ];
                                updatedEditions[index].saved_cover = e.target.value;
                                setEditingGame({
                                  ...editingGame,
                                  editions: updatedEditions,
                                });
                              }}
                              placeholder="https://example.com/edition-cover.jpg"
                            />
                          </div>

                          <div className="form__wrapper">
                            <label
                              htmlFor={`editionTags-${game.id}-${index}`}
                              className="form__label"
                            >
                              Платформы (через запятую)
                            </label>
                            <input
                              type="text"
                              id={`editionTags-${game.id}-${index}`}
                              className="input form__input"
                              value={
                                Array.isArray(edition.tags)
                                  ? edition.tags.join(", ")
                                  : edition.tags || ""
                              }
                              onChange={(e) => {
                                const updatedEditions = [
                                  ...editingGame.editions,
                                ];
                                updatedEditions[index].tags = e.target.value;
                                setEditingGame({
                                  ...editingGame,
                                  editions: updatedEditions,
                                });
                              }}
                              placeholder="PS5, PS4"
                            />
                          </div>
                        </div>
                      ))}
                    </div>
                  )}

                  <div className="form__wrapper">
                    <h3>Аддоны</h3>
                    {editingGame.addons?.map((addon, addonIndex) => (
                      <div key={addonIndex} className="form__addon">
                        <hr className="form__hr" />

                        <div className="form__wrapper">
                          <label
                            htmlFor={`addon-title-${game.id}-${addonIndex}`}
                            className="form__label"
                          >
                            Название аддона
                          </label>
                          <input
                            type="text"
                            id={`addon-title-${game.id}-${addonIndex}`}
                            className="input form__input"
                            value={addon.title || ""}
                            onChange={(e) => {
                              const updatedAddons = [...editingGame.addons];
                              updatedAddons[addonIndex].title = e.target.value;
                              setEditingGame({
                                ...editingGame,
                                addons: updatedAddons,
                              });
                            }}
                          />
                        </div>

                        <div className="form__wrapper">
                          <label
                            htmlFor={`addon-price-${game.id}-${addonIndex}`}
                            className="form__label"
                          >
                            Цена аддона в лирах
                          </label>
                          <input
                            type="text"
                            id={`addon-price-${game.id}-${addonIndex}`}
                            className="input form__input"
                            value={addon.price || ""}
                            onChange={(e) => {
                              const updatedAddons = [...editingGame.addons];
                              updatedAddons[addonIndex].price = e.target.value;
                              setEditingGame({
                                ...editingGame,
                                addons: updatedAddons,
                              });
                            }}
                            placeholder="99,00 TL"
                          />
                        </div>

                        <div className="form__wrapper">
                          <label
                            htmlFor={`addon-convertedPrice-${game.id}-${addonIndex}`}
                            className="form__label"
                          >
                            Цена аддона в рублях
                          </label>
                          <input
                            type="text"
                            id={`addon-convertedPrice-${game.id}-${addonIndex}`}
                            className="input form__input"
                            value={addon.convertedPrice || ""}
                            onChange={(e) => {
                              const updatedAddons = [...editingGame.addons];
                              updatedAddons[addonIndex].convertedPrice =
                                e.target.value;
                              setEditingGame({
                                ...editingGame,
                                addons: updatedAddons,
                              });
                            }}
                            placeholder="999 ₽"
                          />
                        </div>

                        <div className="form__wrapper">
                          <label
                            htmlFor={`addon-price_rub-${game.id}-${addonIndex}`}
                            className="form__label"
                          >
                            Индивидуальная цена в рублях
                          </label>
                          <input
                            type="text"
                            id={`addon-price_rub-${game.id}-${addonIndex}`}
                            className="input form__input"
                            value={addon.price_rub || ""}
                            onChange={(e) => {
                              const updatedAddons = [...editingGame.addons];
                              updatedAddons[addonIndex].price_rub =
                                e.target.value;
                              setEditingGame({
                                ...editingGame,
                                addons: updatedAddons,
                              });
                            }}
                            placeholder="999 ₽"
                          />
                        </div>

                        <div className="form__wrapper">
                          <label
                            htmlFor={`addon-release_date-${game.id}-${addonIndex}`}
                            className="form__label"
                          >
                            Дата выхода аддона
                          </label>
                          <input
                            type="date"
                            id={`addon-release_date-${game.id}-${addonIndex}`}
                            className="input form__input"
                            value={addon.release_date || ""}
                            onChange={(e) =>
                              handleAddonDateChange(e, addonIndex)
                            }
                          />
                        </div>

                        <div className="form__wrapper">
                          <label
                            htmlFor={`addon-type-${game.id}-${addonIndex}`}
                            className="form__label"
                          >
                            Тип аддона
                          </label>
                          <input
                            type="text"
                            id={`addon-type-${game.id}-${addonIndex}`}
                            className="input form__input"
                            value={addon.type || ""}
                            onChange={(e) => {
                              const updatedAddons = [...editingGame.addons];
                              updatedAddons[addonIndex].type = e.target.value;
                              setEditingGame({
                                ...editingGame,
                                addons: updatedAddons,
                              });
                            }}
                            placeholder="DLC, Season Pass"
                          />
                        </div>

                        <div className="form__wrapper">
                          <label
                            htmlFor={`addon-url-${game.id}-${addonIndex}`}
                            className="form__label"
                          >
                            Ссылка на аддон в PS Store
                          </label>
                          <input
                            type="text"
                            id={`addon-url-${game.id}-${addonIndex}`}
                            className="input form__input"
                            value={addon.url || ""}
                            onChange={(e) => {
                              const updatedAddons = [...editingGame.addons];
                              updatedAddons[addonIndex].url = e.target.value;
                              setEditingGame({
                                ...editingGame,
                                addons: updatedAddons,
                              });
                            }}
                            placeholder="https://store.playstation.com/..."
                          />
                        </div>

                        <div className="form__wrapper">
                          <label
                            htmlFor={`addon-translatedGenres-${game.id}-${addonIndex}`}
                            className="form__label"
                          >
                            Жанры (через запятую)
                          </label>
                          <input
                            type="text"
                            id={`addon-translatedGenres-${game.id}-${addonIndex}`}
                            className="input form__input"
                            value={addon.translatedGenres || ""}
                            onChange={(e) => {
                              const updatedAddons = [...editingGame.addons];
                              updatedAddons[addonIndex].translatedGenres =
                                e.target.value;
                              setEditingGame({
                                ...editingGame,
                                addons: updatedAddons,
                              });
                            }}
                            placeholder="Экшен, Приключение"
                          />
                        </div>

                        <div className="form__wrapper">
                          <label
                            htmlFor={`addon-tags-${game.id}-${addonIndex}`}
                            className="form__label"
                          >
                            Тэги (через запятую)
                          </label>
                          <input
                            type="text"
                            id={`addon-tags-${game.id}-${addonIndex}`}
                            className="input form__input"
                            value={
                              Array.isArray(addon.tags)
                                ? addon.tags.join(", ")
                                : addon.tags || ""
                            }
                            onChange={(e) => {
                              const updatedAddons = [...editingGame.addons];
                              updatedAddons[addonIndex].tags = e.target.value;
                              setEditingGame({
                                ...editingGame,
                                addons: updatedAddons,
                              });
                            }}
                            placeholder="DLC, PS5"
                          />
                        </div>

                        <div className="form__wrapper">
                          <label
                            htmlFor={`addon-platforms-${game.id}-${addonIndex}`}
                            className="form__label"
                          >
                            Платформы (через запятую)
                          </label>
                          <input
                            type="text"
                            id={`addon-platforms-${game.id}-${addonIndex}`}
                            className="input form__input"
                            value={
                              Array.isArray(addon.platforms)
                                ? addon.platforms.join(", ")
                                : addon.platforms || ""
                            }
                            onChange={(e) => {
                              const updatedAddons = [...editingGame.addons];
                              updatedAddons[addonIndex].platforms =
                                e.target.value;
                              setEditingGame({
                                ...editingGame,
                                addons: updatedAddons,
                              });
                            }}
                            placeholder="PS5, PS4"
                          />
                        </div>

                        <div className="form__wrapper">
                          <label
                            htmlFor={`addon-description-${game.id}-${addonIndex}`}
                            className="form__label"
                          >
                            Описание
                          </label>
                          <textarea
                            id={`addon-description-${game.id}-${addonIndex}`}
                            className="input form__input"
                            value={addon.description || ""}
                            onChange={(e) => {
                              const updatedAddons = [...editingGame.addons];
                              updatedAddons[addonIndex].description =
                                e.target.value;
                              setEditingGame({
                                ...editingGame,
                                addons: updatedAddons,
                              });
                            }}
                            rows="3"
                          />
                        </div>

                        <div className="form__wrapper">
                          <label
                            htmlFor={`addon-cover-${game.id}-${addonIndex}`}
                            className="form__label"
                          >
                            URL обложки аддона
                          </label>
                          <input
                            type="text"
                            id={`addon-cover-${game.id}-${addonIndex}`}
                            className="input form__input"
                            value={addon.saved_cover || ""}
                            onChange={(e) => {
                              const updatedAddons = [...editingGame.addons];
                              updatedAddons[addonIndex].saved_cover = e.target.value;
                              setEditingGame({
                                ...editingGame,
                                addons: updatedAddons,
                              });
                            }}
                            placeholder="https://example.com/addon-cover.jpg"
                          />
                        </div>
                      </div>
                    ))}
                  </div>

                  <div className="form__buttons">
                    <button
                      type="submit"
                      className="button form__button"
                      disabled={isSubmitting}
                    >
                      {isSubmitting ? "Обновляю игру..." : "Обновить игру"}
                    </button>
                    <button
                      type="button"
                      className="button form__button form__button--delete"
                      onClick={() => openDeleteDialog(game)}
                    >
                      Удалить
                    </button>
                  </div>
                </fieldset>
                <p className="form__advice">
                  * Если устанавливается индивидуальная цена в рублях у игры, то
                  и у соответствующего издания установить ту же цену (и скидку,
                  если изменяется)
                </p>
              </form>
            )}
          </div>
        ))}
      </div>

      {gameMessage && (
        <p className={`form__message form__message--${gameMessageType}`}>
          {gameMessage}
        </p>
      )}

      <Dialog
        isOpen={isDialogOpen}
        onClose={closeDeleteDialog}
        onConfirm={handleConfirmDelete}
        title="Удаление игры"
        message={
          gameToDelete ? (
            <>
              Вы уверены, что хотите удалить игру <br />
              <b>{gameToDelete.title}</b>?
            </>
          ) : (
            "Вы уверены, что хотите удалить эту игру?"
          )
        }
      />
    </div>
  );
};

export default AllGamesTab;
