import React from "react";
import { useNavigate } from "react-router-dom";
import "../App.css";

const BackButton = ({ className }) => {
  const navigate = useNavigate();

  return (
    <button
      onClick={() => navigate(-1)}
      className={`button button--back ${className || ""}`}
    >
      Назад
    </button>
  );
};

export default BackButton;
