import React, { useState, useEffect } from "react";
import { supabase } from "../utils/supabaseClient";

const ExchangeRateTab = () => {
  const [exchangeRate, setExchangeRate] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [message, setMessage] = useState("");
  const [messageType, setMessageType] = useState("");

  useEffect(() => {
    const loadExchangeRate = async () => {
      try {
        const { data, error } = await supabase
          .from("coefficients")
          .select("value")
          .eq("title", "exchange_rate")
          .single();

        if (error) throw error;

        if (data) {
          setExchangeRate(data.value.toString());
        }
      } catch (error) {
        console.error("Ошибка при загрузке курса валют:", error);
        showMessage("Не удалось загрузить курс валют", "error");
      }
    };

    loadExchangeRate();
  }, []);

  const showMessage = (text, type = "success") => {
    setMessage(text);
    setMessageType(type);
    setTimeout(() => {
      setMessage("");
      setMessageType("");
    }, 3000);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      const rateValue = parseFloat(exchangeRate.replace(",", "."));
      if (isNaN(rateValue)) {
        throw new Error("Введите корректное число");
      }

      const { error } = await supabase
        .from("coefficients")
        .upsert(
          { title: "exchange_rate", value: rateValue },
          { onConflict: "title" }
        );

      if (error) throw error;

      showMessage("Курс валют успешно обновлен");
      await updateAllGamePrices(rateValue);
    } catch (error) {
      console.error("Ошибка при обновлении курса валют:", error);
      showMessage(error.message || "Не удалось обновить курс валют", "error");
    } finally {
      setIsSubmitting(false);
    }
  };

  const parseTurkishPrice = (priceStr) => {
    if (!priceStr) return null;

    // Удаляем все нецифровые символы кроме запятой (турецкий формат: 3.149,00)
    const cleaned = priceStr.replace(/[^\d,]/g, "");

    // Заменяем запятую на точку и убираем точку как разделитель тысяч
    const normalized = cleaned.replace(/\./g, "").replace(",", ".");

    return parseFloat(normalized);
  };

  const updateAllGamePrices = async (newRate) => {
    try {
      const { data: games, error: gamesError } = await supabase
        .from("games")
        .select("*");

      if (gamesError) throw gamesError;

      const updates = games.map(async (game) => {
        const updates = {};

        // Основная цена игры
        if (game.price) {
          const priceNumber = parseTurkishPrice(game.price);
          if (!isNaN(priceNumber)) {
            updates.convertedPrice = `${(priceNumber * newRate).toFixed(2)} ₽`;
          }
        }

        // Цены изданий
        if (game.editions?.length > 0) {
          updates.editions = game.editions.map((edition) => {
            if (edition.price) {
              const editionPrice = parseTurkishPrice(edition.price);
              if (!isNaN(editionPrice)) {
                return {
                  ...edition,
                  convertedPrice: `${(editionPrice * newRate).toFixed(2)} ₽`,
                };
              }
            }
            return edition;
          });
        }

        // Цены аддонов
        if (game.addons?.length > 0) {
          updates.addons = game.addons.map((addon) => {
            if (addon.price) {
              const addonPrice = parseTurkishPrice(addon.price);
              if (!isNaN(addonPrice)) {
                return {
                  ...addon,
                  convertedPrice: `${(addonPrice * newRate).toFixed(2)} ₽`,
                };
              }
            }
            return addon;
          });
        }

        if (Object.keys(updates).length > 0) {
          const { error } = await supabase
            .from("games")
            .update(updates)
            .eq("id", game.id);

          if (error) throw error;
        }
      });

      await Promise.all(updates);
      showMessage("Цены успешно пересчитаны по новому курсу");
    } catch (error) {
      console.error("Ошибка при обновлении цен:", error);
      showMessage("Не удалось обновить цены", "error");
    }
  };

  return (
    <form onSubmit={handleSubmit} className="form form--exchange-rate">
      <fieldset className="form__fieldset">
        <legend>Редактировать курс валют</legend>
        <div className="form__wrapper">
          <label htmlFor="exchangeRate" className="form__label">
            Курс валют (1 TRY = X RUB)
          </label>
          <input
            type="text"
            id="exchangeRate"
            className="input form__input"
            value={exchangeRate}
            onChange={(e) => setExchangeRate(e.target.value)}
            placeholder="Введите курс (например 2.5)"
          />
        </div>
        <button
          type="submit"
          className="button form__button"
          disabled={isSubmitting}
        >
          {isSubmitting ? "Обновляю курс..." : "Обновить курс"}
        </button>
      </fieldset>

      {message && (
        <p className={`form__message form__message--${messageType}`}>
          {message}
        </p>
      )}
    </form>
  );
};

export default ExchangeRateTab;
