import React, { useState, useEffect } from "react";
import { DragDropContext, Droppable, Draggable } from "@hello-pangea/dnd";
import Dialog from "./Dialog";
import { supabase } from "../utils/supabaseClient";

const CollectionsTab = React.memo(({ collections, setCollections }) => {
  const [message, setMessage] = useState("");
  const [messageType, setMessageType] = useState("");
  const [editingCollectionId, setEditingCollectionId] = useState(null);
  const [newCollectionTitle, setNewCollectionTitle] = useState("");
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [collectionToDelete, setCollectionToDelete] = useState(null);

  const collectionToDeleteTitle = collections.find(
    (collection) => collection.id === collectionToDelete
  )?.title;

  useEffect(() => {
    if (message) {
      const timer = setTimeout(() => {
        setMessage("");
        setMessageType("");
      }, 3000);
      return () => clearTimeout(timer);
    }
  }, [message]);

  const startEditing = (collectionId, currentTitle) => {
    setEditingCollectionId(collectionId);
    setNewCollectionTitle(currentTitle);
  };

  const saveEditedCollection = async () => {
    if (!editingCollectionId || !newCollectionTitle.trim()) {
      setMessage("Название коллекции не может быть пустым");
      setMessageType("error");
      return;
    }

    try {
      const currentCollection = collections.find(
        (c) => c.id === editingCollectionId
      );
      if (!currentCollection) {
        throw new Error("Коллекция не найдена");
      }

      // Обновляем коллекцию в Supabase
      const { error } = await supabase
        .from("collections")
        .update({ title: newCollectionTitle })
        .eq("id", editingCollectionId);

      if (error) throw error;

      // Обновляем игры, связанные с этой коллекцией
      const { error: gamesError } = await supabase
        .from("games")
        .update({ collection_title: newCollectionTitle })
        .eq("collection_title", currentCollection.title);

      if (gamesError) throw gamesError;

      const updatedCollections = collections.map((collection) =>
        collection.id === editingCollectionId
          ? { ...collection, title: newCollectionTitle }
          : collection
      );

      setCollections(updatedCollections);
      setEditingCollectionId(null);
      setNewCollectionTitle("");
      setMessage("Коллекция успешно обновлена");
      setMessageType("success");
    } catch (error) {
      console.error("Ошибка при обновлении коллекции:", error);
      setMessage("Ошибка при обновлении коллекции");
      setMessageType("error");
    }
  };

  const openDeleteDialog = (collectionId) => {
    setCollectionToDelete(collectionId);
    setIsDialogOpen(true);
  };

  const closeDeleteDialog = () => {
    setIsDialogOpen(false);
    setCollectionToDelete(null);
  };

  const deleteCollection = async (e) => {
    e.preventDefault();
    if (!collectionToDelete) {
      setMessage("ID коллекции не указан");
      setMessageType("error");
      return;
    }

    try {
      // Удаляем коллекцию из Supabase
      const { error } = await supabase
        .from("collections")
        .delete()
        .eq("id", collectionToDelete);

      if (error) throw error;

      // Удаляем связанные игры
      const { error: gamesError } = await supabase
        .from("games")
        .delete()
        .eq("collection_title", collectionToDeleteTitle);

      if (gamesError) throw gamesError;

      const updatedCollections = collections.filter(
        (collection) => collection.id !== collectionToDelete
      );

      setCollections(updatedCollections);
      setMessage("Коллекция и связанные игры успешно удалены");
      setMessageType("success");
    } catch (error) {
      console.error("Ошибка при удалении коллекции:", error);
      setMessage("Ошибка при удалении коллекции");
      setMessageType("error");
    } finally {
      closeDeleteDialog();
    }
  };

  const onDragEnd = async (result) => {
    const { source, destination } = result;

    if (!destination || source.index === destination.index) return;

    const reorderedCollections = Array.from(collections);
    const [removed] = reorderedCollections.splice(source.index, 1);
    reorderedCollections.splice(destination.index, 0, removed);

    // Обновляем порядок в локальном состоянии
    setCollections(reorderedCollections);

    try {
      // Обновляем порядок в Supabase
      for (let i = 0; i < reorderedCollections.length; i++) {
        const collection = reorderedCollections[i];
        const { error } = await supabase
          .from("collections")
          .update({ order: i + 1 })
          .eq("title", collection.title); // Используем поле title

        if (error) throw error;
      }

      setMessage("Порядок коллекций успешно обновлён");
      setMessageType("success");
    } catch (error) {
      console.error("Ошибка при сохранении порядка коллекций:", error);
      setMessage("Ошибка при сохранении порядка коллекций");
      setMessageType("error");
    }
  };

  return (
    <form className="form form--add-game">
      <fieldset className="form__fieldset">
        <legend>Редактировать коллекции*</legend>
        <DragDropContext onDragEnd={onDragEnd}>
          <Droppable droppableId="collections">
            {(provided, snapshot) => (
              <div
                {...provided.droppableProps}
                ref={provided.innerRef}
                className={snapshot.isDraggingOver ? "drag-over" : ""}
              >
                {collections.map((collection, index) => (
                  <Draggable
                    key={collection.id}
                    draggableId={String(collection.id)}
                    index={index}
                  >
                    {(provided, snapshot) => (
                      <div
                        ref={provided.innerRef}
                        {...provided.draggableProps}
                        {...provided.dragHandleProps}
                        className={`form__draggable ${
                          snapshot.isDragging ? "dragging" : ""
                        }`}
                      >
                        {editingCollectionId === collection.id ? (
                          <div className="form__edit-collection">
                            <input
                              type="text"
                              value={newCollectionTitle}
                              onChange={(e) =>
                                setNewCollectionTitle(e.target.value)
                              }
                              className="input form__input"
                            />
                            <div className="form__buttons">
                              <button
                                type="button"
                                onClick={saveEditedCollection}
                                className="button form__button"
                              >
                                Сохранить
                              </button>
                              <button
                                type="button"
                                onClick={() => setEditingCollectionId(null)}
                                className="button form__button form__button--cancel"
                              >
                                Отмена
                              </button>
                              <button
                                type="button"
                                onClick={() => openDeleteDialog(collection.id)}
                                className="button form__button form__button--delete"
                              >
                                Удалить
                              </button>
                            </div>
                          </div>
                        ) : (
                          <div className="form__collection-item">
                            {collection.title}
                            <button
                              type="button"
                              onClick={() =>
                                startEditing(collection.id, collection.title)
                              }
                              className="button form__button form__button--edit"
                            >
                              Редактировать
                            </button>
                          </div>
                        )}
                      </div>
                    )}
                  </Draggable>
                ))}
                {provided.placeholder}
              </div>
            )}
          </Droppable>
        </DragDropContext>
      </fieldset>
      <p className="form__advice">
        * Перетаскиванием элементов можно изменить порядок вывода коллекций на
        главной странице
      </p>
      {message && (
        <p className={`form__message form__message--${messageType}`}>
          {message}
        </p>
      )}

      <Dialog
        isOpen={isDialogOpen}
        onClose={closeDeleteDialog}
        onConfirm={(e) => deleteCollection(e)}
        title="Удаление коллекции"
        message={
          collectionToDeleteTitle ? (
            <>
              Вы уверены, что хотите удалить коллекцию{" "}
              <b>{collectionToDeleteTitle}</b> и все связанные с ней игры?
            </>
          ) : (
            "Вы уверены, что хотите удалить эту коллекцию и все связанные с ней игры?"
          )
        }
      />
    </form>
  );
});

export default React.memo(CollectionsTab);
