import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { supabase } from "../utils/supabaseClient";
import Loader from "../components/Loader";
import BackButton from "../components/BackButton";
import AddToCartButton from "../components/AddToCartButton";

function AddonDetails() {
  const [addonDetails, setAddonDetails] = useState(null);
  const { addonUrl } = useParams();
  const [selectedEditionIndex, setSelectedEditionIndex] = useState(null);
  const searchParams = new URLSearchParams(window.location.hash.split("?")[1]);
  const gameId = searchParams.get("gameId");

  // Функция для удаления HTML-тегов из текста
  // const stripHtml = (html) => {
  //   if (!html) return "";
  //   const tmp = document.createElement("div");
  //   tmp.innerHTML = html;
  //   return tmp.textContent || tmp.innerText || "";
  // };

  // Функция для открытия Google Переводчика с переданным текстом
  // const handleTranslateClick = () => {
  //   if (!addonDetails?.description) return;

  //   const cleanText = stripHtml(addonDetails.description);
  //   const encodedText = encodeURIComponent(cleanText);
  //   const googleTranslateUrl = `https://translate.google.com/?sl=en&tl=ru&text=${encodedText}`;
  //   window.open(googleTranslateUrl, "_blank");
  // };

  useEffect(() => {
    const fetchAddonDetails = async () => {
      try {
        if (!gameId) {
          console.error("Game ID is missing");
          return;
        }

        // 1. Сначала пытаемся найти игру в базе данных
        const { data: gameData, error: gameError } = await supabase
          .from("games")
          .select("*")
          .eq("idGame", gameId)
          .single();

        if (gameError) throw gameError;

        // 2. Ищем аддон в массиве addons игры
        if (gameData.addons && gameData.addons.length > 0) {
          const addon = gameData.addons.find(
            (a) => a.url === decodeURIComponent(addonUrl)
          );

          if (addon) {
            setAddonDetails(addon);
            return;
          }
        }

        // 3. Если аддон не найден, создаем базовую структуру
        setAddonDetails({
          title: "Аддон",
          description: "Описание отсутствует",
          convertedPrice: "Нет данных",
          releaseDate: "Нет информации",
          saved_cover: "",
        });
      } catch (error) {
        console.error("Error fetching addon details:", error);
        // В случае ошибки создаем базовый объект аддона
        setAddonDetails({
          title: "Аддон",
          description: "Не удалось загрузить данные",
          convertedPrice: "Нет данных",
          releaseDate: "Нет информации",
          saved_cover: "",
        });
      }
    };

    fetchAddonDetails();
  }, [addonUrl, gameId]);

  return (
    <div className="app">
      {addonDetails === null ? (
        <Loader />
      ) : (
        <div className="game-details">
          <BackButton className="button--inner-game-page" />
          <div className="game-details-cover-wrapper">
            <img
              src={addonDetails.saved_cover || addonDetails.cover || ""}
              alt={addonDetails.title}
              width="100%"
              height="auto"
              className="game-details-cover"
            />
          </div>
          <div className="game-details-info">
            <h1 className="game-details-title">{addonDetails.title}</h1>
            {addonDetails?.tags && addonDetails.tags.length > 0 && (
              <div className="game-tags">
                {addonDetails.tags.map((tag, index) => (
                  <span key={index} className="game-tag">
                    {tag}
                  </span>
                ))}
              </div>
            )}
            <p className="game-details-price">
              {addonDetails.price_rub || addonDetails.convertedPrice}
            </p>
            <AddToCartButton
              item={{
                title: addonDetails.title,
                url: addonUrl,
                convertedPrice:
                  addonDetails.price_rub || addonDetails.convertedPrice,
                type: "addon",
              }}
            />
            <hr></hr>
            {addonDetails.translatedGenres && (
              <p className="game-details-genre">
                <span>Жанр:</span>
                <span>{addonDetails.translatedGenres}</span>
              </p>
            )}
            {addonDetails?.platforms && addonDetails.platforms.length !== 0 && (
              <p className="game-details-platforms">
                <span>Платформы:</span>
                <span>{addonDetails.platforms.join(", ")}</span>
              </p>
            )}
            <p className="game-details-releasedate">
              <span>Дата выхода:</span>
              <span>{addonDetails.formattedDate || "Нет информации"}</span>
            </p>
            <details>
              <summary className="game-details-description">Описание</summary>
              {/* <button
                onClick={handleTranslateClick}
                className="button button--translate"
              >
                Открыть перевод в Гугл Переводчике
              </button> */}
              <p className="game-details-description-text">
                {addonDetails.description || "Описание отсутствует"}
              </p>
            </details>
          </div>
        </div>
      )}
    </div>
  );
}

export default AddonDetails;
