import React, { useEffect, useState } from "react";
import { useParams, Link } from "react-router-dom";
import { supabase } from "../utils/supabaseClient";
import Loader from "../components/Loader";
import BackButton from "../components/BackButton";
import AddToCartButton from "../components/AddToCartButton";

import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

function GameDetails() {
  const [gameDetails, setGameDetails] = useState(null);
  const [selectedEditionIndex, setSelectedEditionIndex] = useState(null);
  const { gameUrl } = useParams();
  const [isCoverChanging, setIsCoverChanging] = useState(false);

  const sliderSettings = {
    dots: false,
    infinite: false,
    initialSlide: 0,
    speed: 500,
    slidesToShow: 2.4,
    slidesToScroll: 2,
    responsive: [
      {
        breakpoint: 9000,
        settings: {
          slidesToShow: 5,
          slidesToScroll: 5,
        },
      },
      {
        breakpoint: 1140,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 4,
        },
      },
      {
        breakpoint: 960,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
        },
      },
      {
        breakpoint: 767,
        settings: {
          arrows: false,
        },
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 2.4,
          slidesToScroll: 2,
          arrows: false,
        },
      },
      {
        breakpoint: 420,
        settings: {
          slidesToShow: 1.4,
          slidesToScroll: 1,
          arrows: false,
        },
      },
    ],
  };

  const normalizeString = (str) => {
    if (!str) return "";
    return str
      .toLowerCase()
      .trim()
      .replace(/\s+/g, " ")
      .replace(/[\u200B-\u200D\uFEFF]/g, "")
      .replace(/[™®]/g, "")
      .replace(/[^a-z0-9\s]/g, "");
  };

  const updateTagsAndPriceWithSelectedEdition = (
    editionTitle,
    convertedPrice,
    editionDiscount
  ) => {
    if (!gameDetails) return;

    const updatedTags = gameDetails.tags.map((tag) =>
      normalizeString(tag).includes("edition") ? editionTitle : tag
    );

    setGameDetails((prevDetails) => ({
      ...prevDetails,
      tags: updatedTags,
      price: convertedPrice || prevDetails.initialPrice,
      discount: editionDiscount !== null ? editionDiscount : null,
    }));
  };

  // const stripHtml = (html) => {
  //   if (!html) return "";
  //   const tmp = document.createElement("div");
  //   tmp.innerHTML = html;
  //   return tmp.textContent || tmp.innerText || "";
  // };

  // const handleTranslateClick = () => {
  //   const cleanText = stripHtml(gameDetails.description);
  //   const encodedText = encodeURIComponent(cleanText);
  //   const googleTranslateUrl = `https://translate.google.com/?sl=en&tl=ru&text=${encodedText}`;
  //   window.open(googleTranslateUrl, "_blank");
  // };

  useEffect(() => {
    const fetchGameDetails = async () => {
      try {
        // Извлекаем ID игры из URL
        const idGame = gameUrl.split("/").pop();

        // Получаем данные игры из Supabase
        const { data: gameData, error } = await supabase
          .from("games")
          .select("*")
          .eq("idGame", idGame)
          .single();

        if (error) throw error;

        // Сохраняем изначальную цену игры
        const initialPrice = gameData.convertedPrice;

        let selectedEditionIndex = null;

        // 1. Проверяем совпадение между тегами и названиями изданий
        if (gameData.tags && gameData.tags.length > 0) {
          const normalizedTags = gameData.tags.map(normalizeString);
          for (let i = 0; i < gameData.editions.length; i++) {
            const editionTitle = normalizeString(gameData.editions[i].title);
            if (normalizedTags.includes(editionTitle)) {
              selectedEditionIndex = i;
              break;
            }
          }
        }

        // 2. Если совпадений по тегам нет, проверяем совпадение названия игры с названиями изданий
        if (selectedEditionIndex === null) {
          const normalizedGameTitle = normalizeString(gameData.title);
          for (let i = 0; i < gameData.editions.length; i++) {
            const editionTitle = normalizeString(gameData.editions[i].title);
            if (normalizedGameTitle === editionTitle) {
              selectedEditionIndex = i;
              break;
            }
          }
        }

        // 3. Если и это условие не выполнено, ищем издание с подстрокой "Standard"
        if (selectedEditionIndex === null) {
          const standardEditionIndex = gameData.editions.findIndex((edition) =>
            normalizeString(edition.title).includes("standard")
          );
          if (standardEditionIndex !== -1) {
            selectedEditionIndex = standardEditionIndex;
          }
        }

        setGameDetails({
          ...gameData,
          initialTags: gameData.tags,
          initialPrice,
          initialDiscount: gameData.discount || null,
        });

        setSelectedEditionIndex(selectedEditionIndex);

        // Установка данных для выбранного издания
        if (selectedEditionIndex !== null) {
          const selectedEdition = gameData.editions[selectedEditionIndex];
          updateTagsAndPriceWithSelectedEdition(
            selectedEdition.title,
            selectedEdition.convertedPrice,
            selectedEdition.discount
          );
        }
      } catch (error) {
        console.error("Error fetching game details:", error);
      }
    };

    fetchGameDetails();
  }, [gameUrl]);

  const handleEditionClick = (index) => {
    if (selectedEditionIndex === index) {
      setIsCoverChanging(true);
      setTimeout(() => {
        setSelectedEditionIndex(null);
        setGameDetails((prevDetails) => ({
          ...prevDetails,
          tags: prevDetails.initialTags,
          price: prevDetails.initialPrice,
          discount: prevDetails.initialDiscount || null,
        }));
        setIsCoverChanging(false);
      }, 300);
    } else {
      setIsCoverChanging(true);
      setTimeout(() => {
        setSelectedEditionIndex(index);
        if (gameDetails && gameDetails.editions[index]) {
          const selectedEdition = gameDetails.editions[index];
          updateTagsAndPriceWithSelectedEdition(
            selectedEdition.title,
            selectedEdition.convertedPrice,
            selectedEdition.discount
          );
        }
        setIsCoverChanging(false);
      }, 300);
    }
  };

  return (
    <div className="app">
      {gameDetails === null ? (
        <Loader />
      ) : (
        <div className="game-details">
          <BackButton className="button--inner-game-page" />
          <div
            className={`game-details-cover-wrapper ${
              isCoverChanging ? "fade-out" : ""
            }`}
          >
            <img
              src={
                gameDetails.editions &&
                selectedEditionIndex !== null &&
                gameDetails.editions[selectedEditionIndex] &&
                (gameDetails.editions[selectedEditionIndex].saved_cover ||
                  gameDetails.editions[selectedEditionIndex].cover)
                  ? gameDetails.editions[selectedEditionIndex].saved_cover ||
                    gameDetails.editions[selectedEditionIndex].cover
                  : gameDetails.saved_cover || gameDetails.cover
              }
              alt={gameDetails.title}
              width="100%"
              height="100%"
              className={`game-details-cover ${
                isCoverChanging ? "fade-out" : "fade-in"
              }`}
            />
          </div>
          <div className="game-details-info">
            <h1 className="game-details-title">{gameDetails.title}</h1>
            {gameDetails?.tags.length > 0 && (
              <div className="game-tags">
                {gameDetails.tags.map((tag, index) => (
                  <span key={index} className="game-tag">
                    {tag}
                  </span>
                ))}
              </div>
            )}
            <p className="game-details-price">
              {selectedEditionIndex === null
                ? gameDetails.price_rub || gameDetails.initialPrice
                : gameDetails.editions[selectedEditionIndex].price_rub ||
                  gameDetails.editions[selectedEditionIndex]
                    .convertedPrice}{" "}
              {gameDetails.discount && (
                <span className="game-details-discount">
                  {gameDetails.discount}
                </span>
              )}
            </p>
            <AddToCartButton
              item={{
                title: gameDetails.title,
                gameUrl: gameUrl,
                cover:
                  selectedEditionIndex !== null &&
                  gameDetails.editions[selectedEditionIndex] &&
                  (gameDetails.editions[selectedEditionIndex].saved_cover ||
                    gameDetails.editions[selectedEditionIndex].cover)
                    ? gameDetails.editions[selectedEditionIndex].saved_cover ||
                      gameDetails.editions[selectedEditionIndex].cover
                    : gameDetails.saved_cover || gameDetails.cover,
                convertedPrice:
                  selectedEditionIndex === null
                    ? gameDetails.price_rub || gameDetails.initialPrice
                    : gameDetails.editions[selectedEditionIndex].price_rub ||
                      gameDetails.editions[selectedEditionIndex].convertedPrice,
                edition:
                  selectedEditionIndex !== null &&
                  gameDetails.editions[selectedEditionIndex]
                    ? gameDetails.editions[selectedEditionIndex].title
                    : "",
                platforms: gameDetails.platforms,
                type: "game",
              }}
            />
            <hr></hr>
            {gameDetails.translatedGenres && (
              <p className="game-details-genre">
                <span>Жанр:</span>
                <span>{gameDetails.translatedGenres}</span>
              </p>
            )}
            <p className="game-details-platforms">
              <span>Платформы:</span>
              <span>{gameDetails.platforms.join(", ")}</span>
            </p>
            <p className="game-details-voice">
              <span>Озвучка:</span>
              <span>{gameDetails.voice}</span>
            </p>
            <p className="game-details-subtitles">
              <span>Субтитры:</span>
              <span>{gameDetails.subtitles}</span>
            </p>
            <p className="game-details-releasedate">
              <span>Дата выхода:</span>
              <span>{gameDetails.formattedDate}</span>
            </p>
            <details>
              <summary className="game-details-description">Описание</summary>
              {/* <button
                onClick={handleTranslateClick}
                className="button button--translate"
              >
                Открыть перевод в Гугл Переводчике
              </button> */}
              <p className="game-details-description-text">
                {gameDetails.description || "Описание отсутствует"}
              </p>
            </details>

            {gameDetails.editions && gameDetails.editions.length > 0 && (
              <div className="game-details-editions">
                <hr></hr>
                <h2 className="game-details-title game-details-title--editions">
                  Издания
                </h2>
                <Slider {...sliderSettings}>
                  {gameDetails.editions.map((edition, index) => (
                    <li
                      key={index}
                      className={`game-card game-card--edition ${
                        selectedEditionIndex === index ? "selected" : ""
                      }`}
                      onClick={() => handleEditionClick(index)}
                    >
                      <div className="game-cover-wrapper">
                        <img
                          src={edition.saved_cover || edition.cover}
                          alt={edition.title}
                          className="game-cover"
                        />
                        {edition.tags?.length > 0 && (
                          <div className="game-platforms__wrapper">
                            {edition.tags.map((tag, index) => (
                              <span key={index} className="game-platforms">
                                {tag}
                              </span>
                            ))}
                          </div>
                        )}
                      </div>
                      <div className="game-edition-info">
                        <h3 className="game-title">{edition.title}</h3>
                        <p className="game-price">
                          {edition.price_rub || edition.convertedPrice}{" "}
                          {edition.discount && (
                            <span className="game-details-discount">
                              {edition.discount}
                            </span>
                          )}
                        </p>
                      </div>
                    </li>
                  ))}
                </Slider>
                {selectedEditionIndex !== null &&
                  gameDetails.editions[selectedEditionIndex].description &&
                  gameDetails.editions[selectedEditionIndex].description
                    .length > 0 && (
                    <div className="edition-features-block">
                      <h4 className="edition-features-title">
                        Издание включает
                      </h4>
                      <div className="edition-features">
                        {gameDetails.editions[
                          selectedEditionIndex
                        ].description.map((feature, i) => (
                          <p
                            key={i}
                            className="edition-feature game-details-description-text"
                          >
                            {feature}
                          </p>
                        ))}
                      </div>
                    </div>
                  )}
              </div>
            )}

            {gameDetails?.addons.length > 0 && (
              <div className="game-details-addons">
                <hr></hr>
                <h2 className="game-details-title game-details-title--editions">
                  Адд-оны
                </h2>
                <Slider {...sliderSettings}>
                  {gameDetails.addons.map((addon, index) => (
                    <li key={index} className="game-card game-card">
                      <Link
                        to={{
                          pathname: `/addon/${encodeURIComponent(addon.url)}`,
                          search: `?gameId=${gameDetails.idGame}`,
                        }}
                        className="game-card-link"
                      >
                        <div className="game-cover-wrapper">
                          <img
                            src={addon.saved_cover || addon.cover}
                            alt={addon.title}
                            className="game-cover"
                          />
                          {addon.platforms?.length > 0 && (
                            <div className="game-platforms__wrapper">
                              {addon.platforms.map((platform, index) => (
                                <span key={index} className="game-platforms">
                                  {platform}
                                </span>
                              ))}
                            </div>
                          )}
                        </div>
                        <div className="game-edition-info">
                          <span className="game-type">{addon.type}</span>
                          <h3 className="game-title">{addon.title}</h3>
                          <p className="game-price">
                            {addon.price_rub || addon.convertedPrice}
                          </p>
                        </div>
                      </Link>
                    </li>
                  ))}
                </Slider>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

export default GameDetails;
