import React from "react";

const GameCard = React.memo(({ game, onClick, className }) => {
  return (
    <li className={`game-card ${className || ""}`} onClick={onClick}>
      <div className="game-cover-wrapper">
        <img
          src={game.saved_squareCover || game.squareCover || game.cover}
          alt={game.title}
          className="game-cover"
        />
        {game.platforms?.length > 0 && (
          <div className="game-platforms__wrapper">
            {game.platforms.map((platform, index) => (
              <span key={index} className="game-platforms">
                {platform}
              </span>
            ))}
          </div>
        )}
      </div>
      <div className="game-card__info">
      {game?.type && <span className="game-type">{game.type}</span>}
        <h3 className="game-title">{game.title}</h3>
        <p className="game-price">
          {game.price_rub ? `${game.price_rub}` : game.convertedPrice}{" "}
          {game.discount && (
            <span className="game-details-discount game-discount">
              {game.discount}
            </span>
          )}
        </p>
      </div>
    </li>
  );
});

export default GameCard;
