import React, { useState, useEffect } from "react";
import { supabase } from "../utils/supabaseClient";
import ManualAddGameTab from "../components/ManualAddGameTab";
import AllGamesTab from "../components/AllGamesTab";
import CollectionsTab from "../components/CollectionsTab";
import SubscriptionsTab from "../components/SubscriptionsTab";
import ExchangeRateTab from "../components/ExchangeRateTab";
import AddGamesTab from "../components/AddGamesTab";

const AdminPage = () => {
  const [collections, setCollections] = useState([]);
  const [activeTab, setActiveTab] = useState("manualAdd");
  const [subscriptions, setSubscriptions] = useState([]);
  // Состояния для AddGamesTab
  const [gameUrls, setGameUrls] = useState([""]);
  const [selectedCollection, setSelectedCollection] = useState("");
  const [newCollectionTitle, setNewCollectionTitle] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [gameMessage, setGameMessage] = useState("");
  const [gameMessageType, setGameMessageType] = useState("");

  // Загрузка коллекций
  const loadCollections = async () => {
    try {
      const { data, error } = await supabase
        .from("collections")
        .select("*")
        .order("order", { ascending: true });

      if (error) throw error;
      setCollections(data || []);
    } catch (error) {
      console.error("Ошибка при загрузке коллекций:", error);
    }
  };

  // Загрузка подписок
  const loadSubscriptions = async () => {
    try {
      const { data, error } = await supabase
        .from("subscriptions")
        .select("*")
        .order("id");

      if (error) throw error;
      setSubscriptions(
        (data || []).map((sub) => ({
          ...sub,
          durations: sub.durations || [],
        }))
      );
    } catch (error) {
      console.error("Ошибка при загрузке подписок:", error);
    }
  };

  // Обработчик отправки формы AddGamesTab
  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    setGameMessage("");

    try {
      // Здесь должна быть логика обработки формы
      // Например, добавление игр в коллекцию

      setGameMessage("Игры успешно добавлены!");
      setGameMessageType("success");
      setGameUrls([""]); // Сброс полей ввода
      setSelectedCollection("");
      setNewCollectionTitle("");
    } catch (error) {
      console.error("Ошибка при добавлении игр:", error);
      setGameMessage("Произошла ошибка при добавлении игр");
      setGameMessageType("error");
    } finally {
      setIsSubmitting(false);
    }
  };

  useEffect(() => {
    loadCollections();
    loadSubscriptions();
  }, []);

  return (
    <div className="app admin">
      <h1 className="admin__title">Админка</h1>

      <div className="admin__tabs">
      {/* <button
          className={`button admin__tab ${
            activeTab === "addGames" ? "admin__tab--active" : ""
          }`}
          onClick={() => setActiveTab("addGames")}
        >
          Добавить игры
        </button> */}
        <button
          className={`button admin__tab ${
            activeTab === "manualAdd" ? "admin__tab--active" : ""
          }`}
          onClick={() => setActiveTab("manualAdd")}
        >
          Добавить игру
        </button>
        <button
          className={`button admin__tab ${
            activeTab === "editGames" ? "admin__tab--active" : ""
          }`}
          onClick={() => setActiveTab("editGames")}
        >
          Все игры
        </button>
        <button
          className={`button admin__tab ${
            activeTab === "collections" ? "admin__tab--active" : ""
          }`}
          onClick={() => setActiveTab("collections")}
        >
          Коллекции
        </button>
        <button
          className={`button admin__tab ${
            activeTab === "subscriptions" ? "admin__tab--active" : ""
          }`}
          onClick={() => setActiveTab("subscriptions")}
        >
          Подписки
        </button>
        <button
          className={`button admin__tab ${
            activeTab === "exchangeRate" ? "admin__tab--active" : ""
          }`}
          onClick={() => setActiveTab("exchangeRate")}
        >
          Курсы
        </button>
      </div>

      {activeTab === "collections" && (
        <CollectionsTab
          collections={collections}
          setCollections={setCollections}
        />
      )}

      {activeTab === "addGames" && (
        <AddGamesTab
          gameUrls={gameUrls}
          setGameUrls={setGameUrls}
          collections={collections}
          selectedCollection={selectedCollection}
          setSelectedCollection={setSelectedCollection}
          newCollectionTitle={newCollectionTitle}
          setNewCollectionTitle={setNewCollectionTitle}
          handleSubmit={handleSubmit}
          isSubmitting={isSubmitting}
          gameMessage={gameMessage}
          gameMessageType={gameMessageType}
        />
      )}
      {activeTab === "manualAdd" && <ManualAddGameTab />}
      {activeTab === "editGames" && <AllGamesTab collections={collections} />}
      {activeTab === "subscriptions" && (
        <SubscriptionsTab subscriptions={subscriptions} />
      )}
      {activeTab === "exchangeRate" && <ExchangeRateTab />}
    </div>
  );
};

export default AdminPage;
