import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { supabase } from "../utils/supabaseClient";
import Loader from "../components/Loader";
import BackButton from "../components/BackButton";

function SubscriptionInfo() {
  const { subscriptionTitle } = useParams();
  const [subscriptionDetails, setSubscriptionDetails] = useState(null);
  const [subscriptions, setSubscriptions] = useState([]);
  const [selectedDuration, setSelectedDuration] = useState(0);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isCoverChanging, setIsCoverChanging] = useState(false);

  useEffect(() => {
    const fetchSubscriptionData = async () => {
      try {
        setIsLoading(true);

        // Получаем все подписки
        const { data: allSubscriptions, error: subsError } = await supabase
          .from("subscriptions")
          .select("*")
          .order("id");

        if (subsError) throw subsError;

        // Находим текущую подписку
        const currentSubscription = allSubscriptions.find(
          (sub) => sub.title === decodeURIComponent(subscriptionTitle)
        );

        if (!currentSubscription) {
          throw new Error("Подписка не найдена");
        }

        setSubscriptions(allSubscriptions);
        setSubscriptionDetails(currentSubscription);
      } catch (error) {
        console.error("Error fetching subscription data:", error);
        setError("Не удалось загрузить информацию о подписке");
      } finally {
        setIsLoading(false);
      }
    };

    fetchSubscriptionData();
  }, [subscriptionTitle]);

  const handleSubscriptionClick = (subscription) => {
    setIsCoverChanging(true);
    setTimeout(() => {
      setSubscriptionDetails(subscription);
      setSelectedDuration(0); // Сбрасываем выбор длительности при смене подписки
      setIsCoverChanging(false);
    }, 300);
  };

  if (isLoading) {
    return <Loader />;
  }

  if (error) {
    return <div className="error-message">{error}</div>;
  }

  if (!subscriptionDetails) {
    return <div className="error-message">Подписка не найдена</div>;
  }

  return (
    <div className="app">
      <div className="game-details">
        <BackButton className="button--inner-game-page" />
        <div
          className={`game-details-cover-wrapper game-details-cover-wrapper--sub ${
            isCoverChanging ? "fade-out" : ""
          }`}
        >
          <img
            src={subscriptionDetails.cover}
            alt={subscriptionDetails.title}
            className={`game-details-cover game-details-cover--sub ${
              isCoverChanging ? "fade-out" : "fade-in"
            }`}
          />
        </div>
        <div className="game-details-info">
          <h1 className="game-details-title">{subscriptionDetails.title}</h1>
          {subscriptionDetails.durations?.[selectedDuration]?.price && (
            <p className="game-details-price">
              {subscriptionDetails.durations[selectedDuration].price}
            </p>
          )}

          {/* Список всех подписок */}
          <ul className="games-list games-list--horizontal games-list--sub">
            {subscriptions.map((subscription, index) => (
              <li
                key={subscription.id}
                className={`game-card game-card--sub ${
                  subscription.title === subscriptionDetails.title
                    ? "game-card--selected"
                    : ""
                }`}
                onClick={() => handleSubscriptionClick(subscription)}
              >
                <div className="game-cover-wrapper">
                  <img
                    src={subscription.cover}
                    alt={subscription.title}
                    className="game-cover"
                  />
                </div>
              </li>
            ))}
          </ul>

          {/* Выбор длительности */}
          {subscriptionDetails.durations && (
            <div className="game-details-duration-selector">
              {subscriptionDetails.durations.map((duration, index) => (
                <button
                  key={duration.duration}
                  className={`button button--duration ${
                    selectedDuration === index ? "selected" : ""
                  }`}
                  onClick={() => setSelectedDuration(index)}
                >
                  {duration.duration}
                </button>
              ))}
            </div>
          )}

          {subscriptionDetails.description && (
            <blockquote className="game-details-quote">
              {subscriptionDetails.description}
            </blockquote>
          )}
        </div>
      </div>
    </div>
  );
}

export default SubscriptionInfo;
