import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { supabase } from "../utils/supabaseClient";
import Loader from "../components/Loader";
import BackButton from "../components/BackButton";
import GameCard from "../components/GameCard";

function AllCollectionGamesPage() {
  const navigate = useNavigate();
  const { collectionTitle } = useParams();
  const [games, setGames] = useState([]);
  const [page, setPage] = useState(0);
  const [hasMore, setHasMore] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isInitialLoading, setIsInitialLoading] = useState(true);

  useEffect(() => {
    const fetchGames = async () => {
      try {
        setIsLoading(true);

        // Получаем игры из Supabase с пагинацией
        const { data: newGames, error } = await supabase
          .from("games")
          .select("*")
          .eq("collection_title", collectionTitle)
          .order("created_at", { ascending: false })
          .range(page * 24, (page + 1) * 24 - 1);

        if (error) throw error;

        setGames((prevGames) => {
          // Удаляем дубликаты по названию игры
          const uniqueGames = [...prevGames, ...newGames].filter(
            (game, index, self) =>
              index === self.findIndex((g) => g.title === game.title)
          );
          return uniqueGames;
        });

        // Проверяем, есть ли еще данные для загрузки
        setHasMore(newGames.length === 24);
      } catch (error) {
        console.error("Error fetching collection games:", error);
      } finally {
        setIsLoading(false);
        setIsInitialLoading(false);
      }
    };

    fetchGames();
  }, [page, collectionTitle]);

  const loadMore = () => {
    if (!isLoading) {
      setPage((prevPage) => prevPage + 1);
    }
  };

  return (
    <div className="app">
      {isInitialLoading ? (
        <Loader />
      ) : (
        <section className="games-list-section">
          <div className="header">
            <BackButton />
            <h2 className="games-list-title games-list-title--inner-page">
              {collectionTitle}
            </h2>
          </div>
          <ul className="games-list games-list--vertical games-list--inner">
            {games.map((game, index) => (
              <GameCard
                className="game-card--inner-page"
                key={`${game.idGame}-${index}`}
                game={game}
                onClick={() =>
                  navigate(`/game/${encodeURIComponent(game.gameUrl)}`)
                }
              />
            ))}
          </ul>
          {hasMore && games.length >= 24 && (
            <button
              onClick={loadMore}
              className="button button--load-more"
              disabled={isLoading}
            >
              {isLoading ? "Загружаю..." : "Загрузить ещё"}
            </button>
          )}
        </section>
      )}
    </div>
  );
}

export default AllCollectionGamesPage;
