import React, { useState, useEffect, useRef } from "react";
import { supabase } from "../utils/supabaseClient";
// const { createClient } = require("@supabase/supabase-js");
const { getExchangeRate, parsePrice } = require("../utils/priceUtils");

const cleanText = (text) => {
  if (!text) return text;
  // Удаляем только TM и ®, сохраняя все переносы и пробелы
  return text.replace(/[™®]/g, "").replace(/\r\n/g, "\n");
};

// Добавим новую функцию для обработки цен в рублях
const handleRubPriceChange = (e, setStateFunc) => {
  const { name, value } = e.target;
  // Разрешаем цифры, пробелы, точку и символ ₽
  const cleanedValue = value.replace(/[^\d\s.₽]/g, "");
  setStateFunc((prev) => ({ ...prev, [name]: cleanedValue }));
};

const ManualAddGameTab = () => {
  const [formData, setFormData] = useState({
    title: "",
    price: "",
    discount: "",
    voice: "Нет информации",
    subtitles: "Нет информации",
    release_date: "",
    description: "",
    cover: { file: null, name: "" }, // Инициализируем как объект
    saved_cover: { file: null, name: "" }, // Инициализируем как объект
    gameUrl: "",
    translatedGenres: "",
    collection_title: "Предзаказы",
    platforms: "",
    tags: "",
    convertedPrice: "",
    price_rub: "", // Добавлено
  });

  // Рефы для инпутов файлов
  const coverInputRef = useRef(null);
  const savedCoverInputRef = useRef(null);
  const editionCoverInputRef = useRef(null);
  const addonCoverInputRef = useRef(null);

  const [collections, setCollections] = useState([]);

  const [newCollection, setNewCollection] = useState("");
  const [showNewCollectionInput, setShowNewCollectionInput] = useState(false);

  const [editions, setEditions] = useState([]);
  const [addons, setAddons] = useState([]);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [message, setMessage] = useState("");
  const [messageType, setMessageType] = useState("");
  const [loadingCollections, setLoadingCollections] = useState(true);

  // Загрузка коллекций
  useEffect(() => {
    const loadCollections = async () => {
      try {
        const { data, error } = await supabase
          .from("collections")
          .select("title")
          .order("order", { ascending: true });

        if (error) throw error;

        setCollections(data.map((c) => c.title));
        if (data.length > 0) {
          setFormData((prev) => ({ ...prev, collection_title: data[0].title }));
        }
      } catch (error) {
        console.error("Ошибка загрузки коллекций:", error);
        setMessage("Ошибка загрузки коллекций");
        setMessageType("error");
      } finally {
        setLoadingCollections(false);
      }
    };

    loadCollections();
  }, []);

  // обработчик для создания новой коллекции
  const handleAddNewCollection = async () => {
    if (!newCollection.trim()) {
      setMessage("Введите название коллекции");
      setMessageType("error");
      return;
    }

    try {
      // Проверяем, существует ли уже такая коллекция
      const { data: existingCollections, error: checkError } = await supabase
        .from("collections")
        .select("title")
        .eq("title", newCollection.trim());

      if (checkError) throw checkError;

      if (existingCollections.length > 0) {
        setMessage("Такая коллекция уже существует");
        setMessageType("error");
        return;
      }

      // Добавляем новую коллекцию
      const { error: insertError } = await supabase
        .from("collections")
        .insert([
          {
            title: newCollection.trim(),
            order: collections.length + 1, // Добавляем в конец списка
          },
        ])
        .select();

      if (insertError) throw insertError;

      // Обновляем список коллекций
      const { data: updatedCollections, error: fetchError } = await supabase
        .from("collections")
        .select("title")
        .order("order", { ascending: true });

      if (fetchError) throw fetchError;

      setCollections(updatedCollections.map((c) => c.title));
      setFormData((prev) => ({
        ...prev,
        collection_title: newCollection.trim(),
      }));
      setShowNewCollectionInput(false);
      setNewCollection("");
      setMessage("Новая коллекция добавлена");
      setMessageType("success");
    } catch (error) {
      console.error("Ошибка при добавлении коллекции:", error);
      setMessage(error.message || "Ошибка при добавлении коллекции");
      setMessageType("error");
    }
  };

  // Состояния для новых полей изданий и аддонов
  const [editionForm, setEditionForm] = useState({
    id: "",
    tags: "",
    cover: null, // Изменено на файл
    price: "",
    title: "",
    discount: "",
    description: "",
    saved_cover: "",
    price_rub: "", // Добавлено поле для цены в рублях
  });

  const [addonForm, setAddonForm] = useState({
    id: "",
    url: "",
    tags: "",
    type: "",
    cover: null,
    title: "",
    platforms: "",
    description: "",
    release_date: "",
    saved_cover: "",
    convertedPrice: "",
    translatedGenres: "",
    price: "",
    price_rub: "",
  });

  // Обработчики изменений
  const handleChange = (e) => {
    const { name, value, files } = e.target;

    if (files && (name === "cover" || name === "saved_cover")) {
      setFormData((prev) => ({
        ...prev,
        [name]: {
          file: files[0], // Сохраняем сам файл
          name: files[0].name, // Сохраняем только имя файла
        },
      }));
    } else {
      setFormData((prev) => ({ ...prev, [name]: value }));
    }
  };

  // Измененные обработчики для изданий и аддонов
  const handleEditionChange = (e) => {
    const { name, value, files } = e.target;
    if (files && name === "cover") {
      setEditionForm((prev) => ({
        ...prev,
        cover: {
          file: files[0],
          name: files[0].name,
        },
      }));
    } else {
      setEditionForm((prev) => ({
        ...prev,
        [name]: value,
      }));
    }
  };

  const handleAddonChange = (e) => {
    const { name, value, files } = e.target;
    if (files && name === "cover") {
      setAddonForm((prev) => ({
        ...prev,
        cover: {
          file: files[0],
          name: files[0].name,
        },
      }));
    } else {
      setAddonForm((prev) => ({
        ...prev,
        [name]: value,
      }));
    }
  };

  const handleTagsChange = (e) => {
    const { value } = e.target;
    setFormData((prev) => ({
      ...prev,
      tags: value,
    }));
  };

  const handlePlatformsChange = (e) => {
    const { value } = e.target;
    setFormData((prev) => ({
      ...prev,
      platforms: value, // Просто сохраняем строку
    }));
  };

  // Измененные функции добавления изданий и аддонов
  const addEdition = async () => {
    if (!editionForm.title || !editionForm.price) {
      setMessage("Заполните обязательные поля издания");
      setMessageType("error");
      return;
    }

    // Рассчитываем конвертированную цену по аналогии с основной игрой
    let convertedPriceDisplay = "Цена недоступна";
    if (editionForm.price) {
      const tryRate = await getExchangeRate();
      const numericPrice = parsePrice(editionForm.price);
      if (numericPrice) {
        convertedPriceDisplay = `${(numericPrice * tryRate).toFixed(2)} ₽`;
      }
    }

    // Формируем название папки из названия игры
    const folderName = formData.title
      .replace(/\s+/g, "_") // Заменяем пробелы на подчеркивания
      .replace(/[^\w-]/g, ""); // Удаляем специальные символы

    const baseUrl = `http${
      process.env.NODE_ENV === "production" ? "s" : ""
    }://${process.env.REACT_APP_CLIENT}${
      process.env.NODE_ENV !== "production" && process.env.REACT_APP_PORT
        ? `:${process.env.REACT_APP_PORT}`
        : ""
    }/games`;

    const newEdition = {
      ...editionForm,
      id: editionForm.id || generateId(),
      price_rub: editionForm.price_rub || null, // Сохраняем как есть
      convertedPrice: convertedPriceDisplay,
      saved_cover: editionForm.cover
        ? `${baseUrl}/${folderName}/${editionForm.cover.name.replace(
            /\s+/g,
            "_"
          )}`
        : "",
      title: cleanText(editionForm.title),
      description: editionForm.description ? [editionForm.description] : [],
      cover: undefined,
      tags:
        typeof editionForm.tags === "string"
          ? editionForm.tags
              .split(",")
              .map((t) => t.trim())
              .filter((t) => t)
          : editionForm.tags, // если это уже массив, оставляем как есть
      platforms:
        typeof editionForm.platforms === "string"
          ? editionForm.platforms
              .split(",")
              .map((t) => t.trim())
              .filter((t) => t)
          : editionForm.platforms, // если это уже массив, оставляем как есть
    };

    setEditions([...editions, newEdition]);
    setEditionForm({
      id: "",
      tags: [],
      platforms: [],
      cover: null,
      price: "",
      title: "",
      discount: "",
      description: "",
      saved_cover: "",
      price_rub: "", // Очищаем поле цены в рублях
    });
    if (editionCoverInputRef.current) editionCoverInputRef.current.value = "";
  };

  const removeEdition = (index) => {
    setEditions(editions.filter((_, i) => i !== index));
  };

  const addAddon = async () => {
    if (!addonForm.title || !addonForm.type) {
      setMessage("Заполните обязательные поля аддона (название и тип)");
      setMessageType("error");
      return;
    }

    // Рассчитываем конвертированную цену, если она есть
    let convertedPriceDisplay = "";
    if (addonForm.price) {
      const tryRate = await getExchangeRate();
      const numericPrice = parsePrice(addonForm.price);
      if (numericPrice) {
        convertedPriceDisplay = `${(numericPrice * tryRate).toFixed(2)} ₽`;
      }
    }

    // Формируем название папки из названия игры
    const folderName = formData.title
      .replace(/\s+/g, "_") // Заменяем пробелы на подчеркивания
      .replace(/[^\w-]/g, ""); // Удаляем специальные символы

    const baseUrl = `http${
      process.env.NODE_ENV === "production" ? "s" : ""
    }://${process.env.REACT_APP_CLIENT}${
      process.env.NODE_ENV !== "production" && process.env.REACT_APP_PORT
        ? `:${process.env.REACT_APP_PORT}`
        : ""
    }/games`;

    const newAddon = {
      ...addonForm,
      id: addonForm.id || generateId(),
      convertedPrice: convertedPriceDisplay,
      saved_cover: addonForm.cover
        ? `${baseUrl}/${folderName}/${addonForm.cover.name.replace(
            /\s+/g,
            "_"
          )}`
        : "",
      title: cleanText(addonForm.title),
      description: addonForm.description || "",
      formattedDate: addonForm.release_date
        ? new Date(addonForm.release_date).toLocaleDateString("ru-RU")
        : "",
      cover: undefined,
      tags: addonForm.tags
        ? addonForm.tags
            .split(",")
            .map((t) => t.trim())
            .filter((t) => t)
        : [],
      platforms: addonForm.platforms
        ? addonForm.platforms
            .split(",")
            .map((t) => t.trim())
            .filter((t) => t)
        : [],
    };

    setAddons([...addons, newAddon]);

    // Полный сброс формы
    setAddonForm({
      id: "",
      url: "",
      tags: "",
      type: "",
      cover: null,
      title: "",
      platforms: "",
      description: "",
      release_date: "",
      saved_cover: "",
      convertedPrice: "",
      translatedGenres: "",
      price: "",
      price_rub: "",
    });

    // Сброс файлового инпута
    if (addonCoverInputRef.current) addonCoverInputRef.current.value = "";
  };

  const removeAddon = (index) => {
    setAddons(addons.filter((_, i) => i !== index));
  };

  const generateId = () => {
    return `custom-${Math.random().toString(36).substr(2, 9)}`;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      // Рассчитываем конвертированную цену для отображения (всегда)
      let convertedPriceDisplay = "Цена недоступна";
      if (formData.price) {
        const tryRate = await getExchangeRate();
        const numericPrice = parsePrice(formData.price);
        if (numericPrice) {
          convertedPriceDisplay = `${(numericPrice * tryRate).toFixed(2)} ₽`;
        }
      }

      // Формируем название папки из названия игры
      const folderName = formData.title
        .replace(/\s+/g, "_") // Заменяем пробелы на подчеркивания
        .replace(/[^\w-]/g, ""); // Удаляем специальные символы

      const baseUrl = `http${
        process.env.NODE_ENV === "production" ? "s" : ""
      }://${process.env.REACT_APP_CLIENT}${
        process.env.NODE_ENV !== "production" && process.env.REACT_APP_PORT
          ? `:${process.env.REACT_APP_PORT}`
          : ""
      }/games`;

      // Формируем URL для обложек
      const coverUrl = formData.cover.name
        ? `${baseUrl}/${folderName}/${formData.cover.name}`
        : "";

      const savedCoverUrl = formData.saved_cover.name
        ? `${baseUrl}/${folderName}/${formData.saved_cover.name}`
        : "";

      // Очищаем текстовые данные основной игры
      const cleanedFormData = {
        ...formData,
        title: cleanText(formData.title),
        description: cleanText(formData.description),
        collection_title: formData.collection_title,
      };

      // Очищаем издания
      const cleanedEditions = editions.map((edition) => ({
        ...edition,
        title: cleanText(edition.title),
        description: edition.description
          ? edition.description.map((desc) => cleanText(desc))
          : [],
      }));

      // Очищаем аддоны (добавляем полный сброс addonForm)
      const cleanedAddons = addons.map((addon) => ({
        ...addon,
        title: cleanText(addon.title),
        description: cleanText(addon.description),
        formattedDate: addon.release_date
          ? new Date(addon.release_date).toLocaleDateString("ru-RU")
          : "",
        // Добавляем обработку price_rub, если нужно
        price_rub: addon.price_rub || null,
      }));

      // Формируем данные для отправки
      const submissionData = {
        ...cleanedFormData,
        platforms: formData.platforms,
        tags: formData.tags
          .split(",")
          .map((t) => t.trim())
          .filter((t) => t),
        platforms: formData.platforms
          .split(",")
          .map((t) => t.trim())
          .filter((t) => t),
        price: formData.price, // Оригинальная цена в лирах
        price_rub: formData.price_rub || null, // Сохраняем как есть (с ₽ если есть)
        convertedPrice: convertedPriceDisplay, // Всегда конвертированная строка
        formattedDate: new Date(formData.release_date).toLocaleDateString(
          "ru-RU"
        ),
        idGame: formData.gameUrl.split("/").pop() || `custom-${Date.now()}`,
        editions: cleanedEditions.length > 0 ? cleanedEditions : [], // Пустой массив если нет изданий
        addons: cleanedAddons.length > 0 ? cleanedAddons : [], // Пустой массив если нет аддонов
        cover: coverUrl,
        saved_cover: savedCoverUrl,
      };

      // Отправка данных
      const { error } = await supabase.from("games").insert([submissionData]);

      if (error) {
        console.error("Ошибка Supabase:", {
          error,
          submittedData: submissionData,
        });
        throw new Error("Ошибка сохранения в базе данных");
      }

      // Успешное сохранение
      setMessage("Игра успешно добавлена");
      setMessageType("success");

      // Сброс формы
      setFormData({
        title: "",
        price: "",
        discount: "",
        voice: "Нет информации",
        subtitles: "Нет информации",
        release_date: "",
        description: "",
        cover: { file: null, name: "" }, // Исправлено - сбрасываем в объект
        saved_cover: { file: null, name: "" }, // Исправлено - сбрасываем в объект
        gameUrl: "",
        translatedGenres: "",
        collection_title: collections[0] || "Предзаказы",
        platforms: "",
        tags: "",
        price_rub: "",
        convertedPrice: "",
      });
      setEditions([]);
      setAddons([]);
      // Сброс значений файловых инпутов
      if (coverInputRef.current) coverInputRef.current.value = "";
      if (savedCoverInputRef.current) savedCoverInputRef.current.value = "";
    } catch (error) {
      console.error("Ошибка при добавлении игры:", error);
      setMessage(error.message || "Произошла ошибка");
      setMessageType("error");
    } finally {
      setIsSubmitting(false);
      setTimeout(() => setMessage(""), 3000);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="form form--add-game">
      <fieldset className="form__fieldset">
        <legend>Добавить игру</legend>

        <div className="form__wrapper">
          {/* Основные поля игры */}
          <div className="form__wrapper-input">
            <label htmlFor="title" className="form__label">
              Название игры
            </label>
            <input
              type="text"
              id="title"
              name="title"
              className="input form__input"
              value={formData.title}
              onChange={handleChange}
              required
              placeholder="Название игры"
            />
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="price" className="form__label">
              Цена в лирах
            </label>
            <input
              type="text"
              id="price"
              name="price"
              className="input form__input"
              value={formData.price}
              onChange={handleChange}
              required
              placeholder="1.399,00 TL"
            />
            {formData.convertedPrice && (
              <div className="price-converted">{formData.convertedPrice}</div>
            )}
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="price_rub" className="form__label">
              Цена в рублях* (опционально), ₽
            </label>
            <input
              type="text"
              id="price_rub"
              name="price_rub"
              className="input form__input"
              value={formData.price_rub}
              onChange={(e) => handleRubPriceChange(e, setFormData)}
              placeholder="Введите цену в рублях"
            />
            <p className="form__advice">
              * Если устанавливается индивидуальная цена в рублях у игры, то и у
              соответствующего издания установить ту же цену (и скидку, если
              применяется)
            </p>
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="discount" className="form__label">
              Скидка
            </label>
            <input
              type="text"
              id="discount"
              name="discount"
              className="input form__input"
              value={formData.discount}
              onChange={handleChange}
              placeholder="-50%"
            />
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="voice" className="form__label">
              Озвучка
            </label>
            <input
              type="text"
              id="voice"
              name="voice"
              className="input form__input"
              value={formData.voice}
              onChange={handleChange}
              placeholder="Озвучка"
            />
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="subtitles" className="form__label">
              Субтитры
            </label>
            <input
              type="text"
              id="subtitles"
              name="subtitles"
              className="input form__input"
              value={formData.subtitles}
              onChange={handleChange}
              placeholder="Субтитры"
            />
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="release_date" className="form__label">
              Дата выхода
            </label>
            <input
              type="date"
              id="release_date"
              name="release_date"
              className="input form__input"
              value={formData.release_date}
              onChange={handleChange}
              required
            />
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="description" className="form__label">
              Описание
            </label>
            <textarea
              id="description"
              name="description"
              className="input form__input"
              value={formData.description}
              onChange={handleChange}
              required
              placeholder="Описание игры"
              rows="5"
            />
          </div>

          {/* Поля для загрузки обложек */}
          <div className="form__wrapper-input">
            <label htmlFor="cover" className="form__label">
              Квадратная обложка
            </label>
            <input
              type="file"
              id="cover"
              name="cover"
              ref={coverInputRef}
              className="input form__input"
              onChange={handleChange}
              required
              accept="image/*"
            />
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="saved_cover" className="form__label">
              Основная обложка
            </label>
            <input
              type="file"
              id="saved_cover"
              name="saved_cover"
              ref={savedCoverInputRef}
              className="input form__input"
              onChange={handleChange}
              required
              accept="image/*"
            />
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="gameUrl" className="form__label">
              Ссылка на игру в PS Store*
            </label>
            <input
              type="text"
              id="gameUrl"
              name="gameUrl"
              className="input form__input"
              value={formData.gameUrl}
              onChange={handleChange}
              required
              placeholder="URL игры в PS Store"
            />
            <p className="form__advice">
              * Ссылка на игру из PS Store должна быть вида <br></br>
              "https://store.playstation.com/en-tr/product/EP9000-PPSA05684_00-UNCHARTED4R00000"
            </p>
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="translatedGenres" className="form__label">
              Жанры (через запятую)
            </label>
            <input
              type="text"
              id="translatedGenres"
              name="translatedGenres"
              className="input form__input"
              value={formData.translatedGenres}
              onChange={handleChange}
              required
              placeholder="Экшен, Приключение"
            />
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="collection_title" className="form__label">
              Коллекция
            </label>
            {loadingCollections ? (
              <p>Загрузка коллекций...</p>
            ) : (
              <>
                <select
                  id="collection_title"
                  name="collection_title"
                  className="input form__input form__select"
                  value={
                    showNewCollectionInput ? "" : formData.collection_title
                  }
                  onChange={handleChange}
                  required
                  disabled={loadingCollections || showNewCollectionInput}
                >
                  {collections.map((collection, index) => (
                    <option key={index} value={collection}>
                      {collection}
                    </option>
                  ))}
                </select>
                <button
                  type="button"
                  className="button form__button-add-collection"
                  onClick={() => setShowNewCollectionInput(true)}
                >
                  + Новая коллекция
                </button>
                {showNewCollectionInput && (
                  <div className="form__new-collection">
                    <input
                      type="text"
                      className="input form__input"
                      value={newCollection}
                      onChange={(e) => setNewCollection(e.target.value)}
                      placeholder="Введите название новой коллекции"
                    />
                    <button
                      type="button"
                      className="button form__button-confirm"
                      onClick={handleAddNewCollection}
                    >
                      Подтвердить
                    </button>
                    <button
                      type="button"
                      className="button form__button-cancel"
                      onClick={() => {
                        setShowNewCollectionInput(false);
                        setNewCollection("");
                      }}
                    >
                      Отмена
                    </button>
                  </div>
                )}
              </>
            )}
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="platforms" className="form__label">
              Платформы (через запятую)
            </label>
            <input
              type="text"
              id="platforms"
              name="platforms"
              className="input form__input"
              value={formData.platforms}
              onChange={handlePlatformsChange}
              required
              placeholder="PS5, PS4"
            />
          </div>

          <div className="form__wrapper-input">
            <label htmlFor="tags" className="form__label">
              Тэги (через запятую)
            </label>
            <input
              type="text"
              id="tags"
              name="tags"
              className="input form__input"
              value={formData.tags}
              onChange={handleTagsChange}
              placeholder="PS5, Standard Edition"
            />
          </div>
        </div>

        {/* Секция для добавления изданий */}
        <div className="form__section">
          <h3>Издания</h3>
          <div className="form__wrapper">
            <div className="form__wrapper-input">
              <label htmlFor="edition-title" className="form__label">
                Название издания
              </label>
              <input
                type="text"
                id="edition-title"
                name="title"
                className="input form__input"
                value={editionForm.title}
                onChange={handleEditionChange}
                placeholder="Deluxe Edition"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="edition-price" className="form__label">
                Цена издания в лирах
              </label>
              <input
                type="text"
                id="edition-price"
                name="price"
                className="input form__input"
                value={editionForm.price}
                onChange={handleEditionChange}
                placeholder="2.099,00 TL"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="edition-price_rub" className="form__label">
                Цена издания в рублях* (опционально), ₽
              </label>
              <input
                type="text"
                id="edition-price_rub"
                name="price_rub"
                className="input form__input"
                value={editionForm.price_rub}
                onChange={(e) => handleRubPriceChange(e, setEditionForm)}
                placeholder="Введите цену в рублях"
              />
              <p className="form__advice">
                * Если устанавливается индивидуальная цена в рублях у издания,
                то и у основная цены установить то же значение (и скидку, если
                применяется)
              </p>
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="edition-discount" className="form__label">
                Скидка издания
              </label>
              <input
                type="text"
                id="edition-discount"
                name="discount"
                className="input form__input"
                value={editionForm.discount}
                onChange={handleEditionChange}
                placeholder="-50%"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="edition-tags" className="form__label">
                Платформы (через запятую, опционально)
              </label>
              <input
                type="text"
                id="edition-tags"
                name="tags"
                className="input form__input"
                value={editionForm.tags}
                onChange={handleEditionChange}
                placeholder="PS4, PS5"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="edition-cover" className="form__label">
                Обложка издания
              </label>
              <input
                type="file"
                id="edition-cover"
                name="cover"
                ref={editionCoverInputRef}
                className="input form__input"
                onChange={handleEditionChange}
                accept="image/*"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="edition-description" className="form__label">
                Описание
              </label>
              <textarea
                id="edition-description"
                name="description"
                className="input form__input"
                value={editionForm.description}
                onChange={handleEditionChange}
                placeholder="Описание издания"
                rows="3"
              />
            </div>

            <button
              type="button"
              className="button form__button form__button-add"
              onClick={addEdition}
            >
              Добавить издание
            </button>
          </div>

          {editions.length > 0 && (
            <div className="form__list">
              <h4>Добавленные издания:</h4>
              <ul>
                {editions.map((edition, index) => (
                  <li key={index}>
                    {edition.title} - {edition.price}
                    <button
                      type="button"
                      className="button form__button-remove"
                      onClick={() => removeEdition(index)}
                    >
                      Удалить
                    </button>
                  </li>
                ))}
              </ul>
            </div>
          )}
        </div>

        {/* Секция для добавления аддонов */}
        <div className="form__section">
          <h3>Аддоны</h3>
          <div className="form__wrapper">
            <div className="form__wrapper-input">
              <label htmlFor="addon-title" className="form__label">
                Название аддона
              </label>
              <input
                type="text"
                id="addon-title"
                name="title"
                className="input form__input"
                value={addonForm.title}
                onChange={handleAddonChange}
                placeholder="Название аддона"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="addon-price" className="form__label">
                Цена аддона в лирах
              </label>
              <input
                type="text"
                id="addon-price"
                name="price"
                className="input form__input"
                value={addonForm.price}
                onChange={handleAddonChange}
                placeholder="99,00 TL"
              />
              {addonForm.convertedPrice && (
                <div className="price-converted">
                  {addonForm.convertedPrice}
                </div>
              )}
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="addon-price_rub" className="form__label">
                Цена аддона в рублях (опционально)
              </label>
              <input
                type="text"
                id="addon-price_rub"
                name="price_rub"
                className="input form__input"
                value={addonForm.price_rub}
                onChange={(e) => handleRubPriceChange(e, setAddonForm)}
                placeholder="Введите цену в рублях"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="addon-release_date" className="form__label">
                Дата выхода аддона
              </label>
              <input
                type="date"
                id="addon-release_date"
                name="release_date"
                className="input form__input"
                value={addonForm.release_date}
                onChange={handleAddonChange}
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="addon-type" className="form__label">
                Тип аддона
              </label>
              <input
                type="text"
                id="addon-type"
                name="type"
                className="input form__input"
                value={addonForm.type}
                onChange={handleAddonChange}
                placeholder="Игровая валюта"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="addon-cover" className="form__label">
                Обложка аддона
              </label>
              <input
                type="file"
                id="addon-cover"
                name="cover"
                ref={addonCoverInputRef}
                className="input form__input"
                onChange={handleAddonChange}
                accept="image/*"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="addon-url" className="form__label">
                Ссылка на аддон в PS Store
              </label>
              <input
                type="text"
                id="addon-url"
                name="url"
                className="input form__input"
                value={addonForm.url}
                onChange={handleAddonChange}
                placeholder="URL аддона в PS Store"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="addon-translatedGenres" className="form__label">
                Жанры (через запятую)
              </label>
              <input
                type="text"
                id="addon-translatedGenres"
                name="translatedGenres"
                className="input form__input"
                value={addonForm.translatedGenres}
                onChange={handleAddonChange}
                placeholder="Экшен, Приключение"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="addon-tags" className="form__label">
                Тэги (через запятую)
              </label>
              <input
                type="text"
                id="addon-tags"
                name="tags"
                className="input form__input"
                value={addonForm.tags}
                onChange={handleAddonChange}
                placeholder="PS5, PS4"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="addon-platforms" className="form__label">
                Платформы (через запятую)
              </label>
              <input
                type="text"
                id="addon-platforms"
                name="platforms"
                className="input form__input"
                value={addonForm.platforms}
                onChange={handleAddonChange}
                placeholder="PS5, PS4"
              />
            </div>

            <div className="form__wrapper-input">
              <label htmlFor="addon-description" className="form__label">
                Описание
              </label>
              <textarea
                id="addon-description"
                name="description"
                className="input form__input"
                value={addonForm.description}
                onChange={handleAddonChange}
                placeholder="Описание аддона"
                rows="3"
              />
            </div>

            <button
              type="button"
              className="button form__button form__button-add"
              onClick={addAddon}
            >
              Добавить аддон
            </button>
          </div>

          {addons.length > 0 && (
            <div className="form__list">
              <h4>Добавленные аддоны:</h4>
              <ul>
                {addons.map((addon, index) => (
                  <li key={index}>
                    {addon.title} - {addon.type}
                    <button
                      type="button"
                      className="button form__button-remove"
                      onClick={() => removeAddon(index)}
                    >
                      Удалить
                    </button>
                  </li>
                ))}
              </ul>
            </div>
          )}
        </div>

        <button
          type="submit"
          className="button button--main form__button"
          disabled={isSubmitting}
        >
          {isSubmitting ? "Добавляю игру..." : "Добавить игру"}
        </button>
      </fieldset>

      {message && (
        <p className={`form__message form__message--${messageType}`}>
          {message}
        </p>
      )}
    </form>
  );
};

export default ManualAddGameTab;
